<template>
    <div class="sr_table" :class="{ noData: data && data.length == 0 }">
        <el-table
            ref="SrTable"
            @select-all="selectAll"
            @selection-change="selectChange"
            @row-click="rowClick"
            @row-dblclick="rowDblclick"
            @sort-change="sortChange"
            :highlight-current-row="config.highlightCurrentRow"
            :data="tableData"
            :height="config.height"
            :max-height="config.maxHeight"
            :show-summary="config.showSummary"
            :sum-text="config.sumText || ''"
            :row-key="config.rowKey || 'id'"
            :lazy="config.lazy || true"
            :default-expand-all="config.expandAll || false"
            :load="config.load || (() => {})"
            :tree-props="
                config.treeProps || {
                    children: 'children',
                    hasChildren: 'hasChildren',
                }
            "
            :row-class-name="config.rowClassName"
            :cell-class-name="config.cellClassName"
            tooltip-effect="light"
            tooltip-placement="top-start"
            v-bind="$attrs"
        >
            <el-table-column
                v-if="config.selectionConfig && config.selectionConfig.display"
                align="left"
                type="selection"
                :selectable="config.selectionConfig.selectable"
                :reserve-selection="true"
                :width="config.selectionConfig.width || 50"
            ></el-table-column>
            <el-table-column
                :label="config.indexConfig.label"
                type="index"
                :width="config.indexConfig.width || 52"
                v-if="config.indexConfig && config.indexConfig.display"
            ></el-table-column>
            <el-table-column
                :prop="item.prop"
                :label="item.label"
                :width="item.width"
                :min-width="item.minWidth"
                :sortable="item.sortable || false"
                v-for="(item, index) in config.header"
                :key="index"
                :show-overflow-tooltip="true"
            >
                <template slot-scope="scope">
                    <slot
                        name="cellSlot"
                        v-if="item.cellSlot"
                        :scope="{
                            row: scope.row,
                            item: item,
                            index: item.index,
                        }"
                    ></slot>
                    <span
                        :class="item.className"
                        v-else-if="item.hidPopover && item.filter"
                        >{{
                            filters[item.filter](scope.row[item.prop]) || "-"
                        }}</span
                    >
                    <span
                        class="cell-box"
                        :class="item.className"
                        v-else-if="item.hidPopover && !item.fileter"
                        >{{ scope.row[item.prop] || "-" }}</span
                    >
                    <div v-else-if="item.filter">
                        {{ filters[item.filter](scope.row[item.prop]) || "-" }}
                    </div>
                    <div v-else class="hid" :class="item.className">
                        {{ scope.row[item.prop] || "-" }}
                    </div>
                </template>
            </el-table-column>
            <el-table-column
                v-if="config.operateConfig && config.operateConfig.display"
                :fixed="config.operateConfig.fixed"
                :label="config.operateConfig.label"
                :width="config.operateConfig.width || 156"
                class="operate"
            >
                <template slot="header">
                    <div style="height: 100%">
                        {{ config.operateConfig.label }}
                        <el-popover
                            ref="popover1"
                            placement="top"
                            trigger="hover"
                            v-if="!config.operateConfig.hideHeaderSetting"
                            content=""
                        >
                            <el-button
                                slot="reference"
                                class="sr-icon-set"
                                type="text"
                                :style="`right: ${exportIndex ? 40 : 0}px`"
                                @click="settingHeader"
                            ></el-button>
                        </el-popover>
                        <el-popover
                            ref="popover2"
                            placement="top"
                            trigger="hover"
                            v-if="!config.operateConfig.hideHeaderexport"
                            content=""
                        >
                            <el-button
                                slot="reference"
                                class="sr-icon-packing-labeling"
                                type="text"
                                v-if="exportIndex"
                                @click="config.operateConfig.export"
                            ></el-button>
                        </el-popover>
                    </div>
                </template>
                <div class="operate_btns" slot-scope="scope">
                    <template
                        v-for="(item, index) in scope.row.btns.slice(
                            0,
                            scope.row.btns.length <= 3 ? 3 : 2
                        )"
                    >
                        <el-button
                            :key="index"
                            size="medium"
                            :type="item.type || 'text'"
                            @click="
                                () => {
                                    scope.row.path(scope.row);
                                }
                            "
                            v-if="item.btnName"
                            >{{ item.name }}</el-button
                        >
                        <el-button
                            :key="index"
                            :type="item.type"
                            size="medium"
                            @click.stop="callFn(item, scope.row, item.name)"
                            v-else
                            >{{ item.name }}</el-button
                        >
                    </template>

                    <el-popover
                        placement="bottom-end"
                        width="auto"
                        trigger="hover"
                        class="more_popover"
                        popper-class="sr_table_popover"
                        v-if="scope.row.btns && scope.row.btns.length > 3"
                    >
                        <div class="popover_container">
                            <ul class="infinite-list" style="overflow: auto">
                                <template
                                    v-for="(
                                        item, index
                                    ) in scope.row.btns.slice(2)"
                                >
                                    <li
                                        :key="index"
                                        class="infinite-list-item"
                                        @click="
                                            () => {
                                                scope.row.path(scope.row);
                                            }
                                        "
                                        v-if="item.btnName"
                                    >
                                        {{ item.name }}
                                    </li>
                                    <li
                                        :key="index"
                                        class="infinite-list-item"
                                        @click.stop="
                                            callFn(item, scope.row, item.name)
                                        "
                                        v-else
                                    >
                                        {{ item.name }}
                                    </li>
                                </template>
                            </ul>
                        </div>
                        <el-button
                            @click.stop
                            class="more_btns"
                            slot="reference"
                            type="text"
                        >
                            <i
                                class="sr-icon-ellipsis"
                                style="transform: rotate(90deg)"
                            ></i>
                        </el-button>
                    </el-popover>
                </div>
            </el-table-column>
            <slot name="empty" slot="empty">
                <div class="table_empty">
                    <img :src="emptyImg" class="empty_img" />
                    <span class="empty_null">{{
                        (config.emptyConfig && config.emptyConfig.emptyNull) ||
                        ""
                    }}</span>
                    <div
                        class="empty_tip"
                        v-if="config.emptyConfig && config.emptyConfig.emptyTip"
                    >
                        {{ config.emptyConfig.emptyTip }}
                        <span class="empty_btn" @click="emptyClick">
                            {{ config.emptyConfig.emptyBtn }}
                        </span>
                    </div>
                </div>
            </slot>
        </el-table>
        <div class="sr_table_bottom">
            <div>
                <slot name="leftBottomSlot"></slot>
            </div>
            <div>
                <el-pagination
                    class="pagination"
                    v-if="page.total > (page.minSize || 10)"
                    @size-change="handleSizeChange"
                    @current-change="handleChange"
                    :current-page="page.pageNum || 1"
                    :layout="
                        page.layout || 'total, sizes, prev, pager, next, jumper'
                    "
                    :total="page.total"
                    :page-size="page.pageSize || 10"
                    :page-sizes="page.pageSizes || [10, 20, 30]"
                ></el-pagination>
            </div>
        </div>
        <SrSettingTable
            :data="config.operateConfig.allTableHeader"
            @close="() => (dialogVisible = false)"
            :dialogVisible="dialogVisible"
            @saveHeaderList="saveHeaderList"
            v-if="
                config.operateConfig &&
                config.operateConfig.allTableHeader &&
                config.operateConfig.allTableHeader.length &&
                dialogVisible
            "
        ></SrSettingTable>
    </div>
</template>

<script>
import emptyImg from "./empty.png";
import utils from "../SrUtils/index.js";
export default {
    name: "SrTable",
    props: {
        filters: Object,
        maxHeight: {
            type: [String, Number],
            default() {
                return 1000;
            },
        },
        data: {
            type: Array,
            default() {
                return [];
            },
        },
        page: {
            type: Object,
            default() {
                return {
                    total: 0,
                    pageSize: 5,
                    pageSizes: [5, 10, 20, 30],
                    pageNum: 1,
                };
            },
        },
        config: {
            type: Object,
            default() {
                return {};
            },
        },
        showPagination: {
            tyep: Boolean,
            default: true,
        },
    },

    data() {
        return {
            dialogVisible: false,
            emptyImg: emptyImg,
            utils: utils,
        };
    },
    computed: {
        exportIndex() {
            return (
                this.config.operateConfig.export &&
                this.utils.checkCodeInArr(
                    this.config.operateConfig.exportPermissionCode
                )
            );
        },
        tableData: {
            get: function () {
                let tableData = this.data.map((row) => {
                    row.btns = [];
                    if (this.config.innerBtn && this.config.innerBtn.length) {
                        this.config.innerBtn.forEach((item) => {
                            if (this.isShowBtn(item, row)) {
                                row.btns.push(item);
                            }
                        });
                    }

                    if (row.operationBtns && row.operationBtns.length) {
                        row.operationBtns.forEach((item) => {
                            row.btns.push({ ...item, name: item.btnName });
                        });
                    }
                    return row;
                });
                return tableData;
            },
            set: function (val) {},
        },
    },
    mounted() {},
    methods: {
        //
        callFn(item, row, name, index) {
            if (item.method && item.method.constructor == Function) {
                // fn
                item.method(item, row, name, index);
                return;
            }
            let sum = 0;
            let obj = this.getDeepMethod(this.$parent, item.method, sum, 10);
            if (obj.parent && obj.method) {
                obj.method.call(obj.parent, row, name, index);
            }
        },
        saveHeaderList(data) {
            this.dialogVisible = false;
            this.config.operateConfig &&
                this.config.operateConfig.saveHeaderList(data);
        },
        settingHeader() {
            this.dialogVisible = true;
        },
        // method
        getDeepMethod(parent, method, sum, limit) {
            sum++;
            if (!parent) {
                return {};
            } else if (sum == limit) {
                return {};
            } else if (parent[method]) {
                return {
                    parent,
                    method: parent[method],
                };
            } else {
                return this.getDeepMethod(parent.$parent, method);
            }
        },
        isShowBtn(item, row) {
            // let role = this.roleBtn(item.code || "");
            let field = true;
            let arr =
                item.value == undefined
                    ? []
                    : item.value.constructor == Array
                    ? item.value
                    : item.value
                    ? [item.value]
                    : [];
            if (item.reverse) {
                // 
                field = false;
                arr.map((res) => {
                    if (row[item.field] == res) {
                        // 
                        field = true;
                    }
                });
            } else {
                arr.map((res) => {
                    if (row[item.field] == res) {
                        // 
                        field = false;
                    }
                });
            }
            let validate = true;
            if (item.validate) {
                // 
                validate = item.validate(item, row);
            }
            return (!item.field || field) && validate;
        },
        //
        handleChange(page) {
            this.$emit("pageChanged", page);
        },
        // 
        handleSizeChange(size) {
            this.$emit("sizeChanged", size);
        },
        // 
        selectChange(val) {
            this.$emit("selected", val);
        },
        // 
        rowClick(row) {
            this.$emit("clickRow", row);
        },
        // 
        rowDblclick(row) {
            this.$emit("rowDblclick", row);
        },
        // 
        sortChange(column) {
            column.configdata = this.config.header;
            this.$emit("sortChange", column);
        },
        // 
        selectAll(selection) {
            this.$emit("selectAll", selection);
        },
        emptyClick() {
            this.$emit("emptyClick");
        },
        // table
        clearLazyTreeNodeMap(pid) {
            this.$set(this.$refs.SrTable.store.states.lazyTreeNodeMap, pid, []);
        },
    },
};
</script>

<style lang="scss">
</style>



