<template>
  <div>
    <SrTrace
      :trackData="trackData"
      :defaultProps="defaultProps"
      :imgUrl="imgUrl"
      ref="sr_trace_track"
    ></SrTrace>

    <div class="traceTrackBtn" @click="open">
      <slot></slot>
    </div>
  </div>
</template>

<script>
import SrTrace from "./SrTraceTrack.vue";
export default {
  name: "SrTraceTrack",
  components: {
    SrTrace,
  },
  data() {
    return {
      // 
      defaultProps: {
        name: "name",
        value: "value",
        fileList: "fileList",
        list: "list",
      },
    };
  },
  props: {
    trackData: {
      type: Array,
      default() {
        return [];
      },
    },
    imgUrl: {
      type: String,
      default() {
        return "";
      },
    },
  },
  methods: {
    open() {
      this.$refs.sr_trace_track.open();
    },
  },
};
</script>

<style>
</style>


