<template>
  <div :value="value">
    <div>
      <el-date-picker
        class="sr-date-picker"
        :type="item.timeType ? item.timeType : 'datetime'"
        :disabled="item.disabled"
        v-model="date[startname]"
        :placeholder="''"
        :picker-options="pickerOptionsStart"
        :clearable="false"
        @change="
          (val) => {
            change(val, 1);
          }
        "
        :value-format="item.format ? item.format : 'yyyy-MM-dd HH:mm:ss'"
      ></el-date-picker>
    </div>
    <div>
      <el-date-picker
        :type="item.timeType ? item.timeType : 'datetime'"
        :disabled="item.disabled"
        v-model="date[endname]"
        :placeholder="''"
        :picker-options="pickerOptionsEnd"
        :clearable="false"
        @change="
          (val) => {
            change(val, 2);
          }
        "
        default-time="23:59:59"
        :value-format="item.format ? item.format : 'yyyy-MM-dd HH:mm:ss'"
      ></el-date-picker>
    </div>
  </div>
</template>

<!--
  
  
  20201119 17:09:02
  v1.0
-->
<script>
export default {
  name: "SrDatePicker",
  model: {
    prop: "value",
    event: "change",
  },
  props: {
    value: {
      type: Object | Boolean,
      default: function () {
        return {};
      },
    },
    item: {
      type: Object | Array,
      default() {
        return {};
      },
    },
    startname: {
      type: String,
      default: "startTime",
    },
    endname: {
      type: String,
      default: "endTime",
    },
    flag: {
      type: Boolean,
      default: true,
    },
  },
  watch: {
    value: {
      handler(val) {
        this.date = val;
      },
      deep: true,
    },
  },
  data() {
    return {
      date: this.value,
      pickerOptionsStart: {
        disabledDate: (time) => {
          let delay = this.date[this.endname];
          if (delay) {
            return time.getTime() >= new Date(delay).getTime();
          }
        },
      },
      pickerOptionsEnd: {
        disabledDate: (time) => {
          let delay = this.date[this.startname];
          if (delay) {
            return time.getTime() <= new Date(delay).getTime();
          }
        },
      },
    };
  },
  methods: {
    change(value, type) {
      if (type == 1) {
        this.date[this.startname] = value;
      } else {
        this.date[this.endname] = value;
      }
      this.checkValue();

      this.$emit("change", {
        [this.startname]: this.date[this.startname],
        [this.endname]: this.date[this.endname],
      });
    },
    checkValue() {
      let flag = "";
      if (this.date[this.startname] && this.date[this.endname]) {
        flag = this.date;
      }
      this.$emit("changeModel", flag);
    },
  },
};
</script>



