<template>
    <div class="sr_kind_editor">
        <textarea :id="id" name="content" v-model="outContent" :maxlength="5"></textarea>
        <SrBigUpload
            :item="uploadItem"
            ref="srbigupload"
            style="display: none"
            @uploadImgSuccess="uploadSuccess"
        />
        
    </div>
</template>

<script>
import "kindeditor/kindeditor-all-min.js";
import "kindeditor/themes/default/default.css";
import "kindeditor/lang/zh-CN.js";

export default {
    name: "SrKindEditor",
    data() {
        return {
            editor: null,
            outContent: this.content,
            contentLength: 0,
            uploadItem: {
                limit: 500,
                fileType: "jpg,jpeg,jpe,png",
                target: "editor",
            },
        };
    },
    inject: ["baseUrl", "imgBaseUrl", "uploadBaseUrl"],
    props: {
        // 
        readonlyMode: {
            type: Boolean,
            default: false,
        },
        header: {
            type: Object,
            default: () => ({}),
        },
        content: {
            type: String,
            default: "",
        },
        id: {
            type: String,
            required: true,
        },
        width: {
            type: String,
            default: "100%",
        },
        height: {
            type: String,
        },
        minWidth: {
            type: Number,
            default: 1000,
        },
        minHeight: {
            type: Number,
            default: 71,
        },
        items: {
            type: Array,
            default: function () {
                return [
                    "formatblock",
                    // "fontname",
                    "fontsize",
                    "|",
                    "forecolor", // 
                    "hilitecolor", // 
                    "bold", // 
                    "italic", // 
                    "underline", // 
                    "strikethrough", // 
                    "lineheight", // 
                    "|",
                    "justifyleft",
                    "justifycenter",
                    "justifyright",
                    "justifyfull",
                    "insertorderedlist",
                    "insertunorderedlist",
                    "indent",
                    "outdent",
                    "|",
                    "emoticons", // 
                    "image", // 
                    "table", // 
                    "link", // 
                    "unlink", // 
                    "|",
                    "removeformat", // 
                    "undo", //
                    "redo", //
                    "fullscreen", // 
                    // "source", // html
                    "about", // 
                    // "preview",//
                    // "print",//
                    // "template",// 
                    // "code",// 
                    // "cut",// 
                    // "copy", // 
                    // "paste", // 
                    // "plainpaste", // 
                    // "wordpaste", // word
                    // "subscript", // 
                    // "superscript", // 
                    // "clearhtml", // html
                    // "quickformat", // 
                    // "selectall", // 
                    // "/",
                    // "multiimage", // 
                    // "flash",
                    // "media",
                    // "insertfile", // 
                    // "hr", // 
                    // "baidumap", // 
                    // "pagebreak", // 
                    // "anchor", //
                ];
            },
        },
        noDisableItems: {
            type: Array,
            default: function () {
                return ["source", "fullscreen"];
            },
        },
        filterMode: {
            type: Boolean,
            default: true,
        },
        htmlTags: {
            type: Object,
            default: function () {
                return {
                    font: ["color", "size", "face", ".background-color"],
                    span: ["style"],
                    div: ["class", "align", "style"],
                    table: [
                        "class",
                        "border",
                        "cellspacing",
                        "cellpadding",
                        "width",
                        "height",
                        "align",
                        "style",
                    ],
                    "td,th": [
                        "class",
                        "align",
                        "valign",
                        "width",
                        "height",
                        "colspan",
                        "rowspan",
                        "bgcolor",
                        "style",
                    ],
                    a: ["class", "href", "target", "name", "style"],
                    embed: [
                        "src",
                        "width",
                        "height",
                        "type",
                        "loop",
                        "autostart",
                        "quality",
                        "style",
                        "align",
                        "allowscriptaccess",
                        "/",
                    ],
                    img: [
                        "src",
                        "width",
                        "height",
                        "border",
                        "alt",
                        "title",
                        "align",
                        "style",
                        "/",
                    ],
                    hr: ["class", "/"],
                    br: ["/"],
                    "p,ol,ul,li,blockquote,h1,h2,h3,h4,h5,h6": [
                        "align",
                        "style",
                    ],
                    "tbody,tr,strong,b,sub,sup,em,i,u,strike": [],
                };
            },
        },
        wellFormatMode: {
            type: Boolean,
            default: true,
        },
        resizeType: {
            type: Number,
            default: 0,
        },
        themeType: {
            type: String,
            default: "default",
        },
        langType: {
            type: String,
            default: "zh-CN",
        },
        designMode: {
            type: Boolean,
            default: true,
        },
        fullscreenMode: {
            type: Boolean,
            default: false,
        },
        basePath: {
            type: String,
            default: "",
        },
        themesPath: {
            type: String,
            default: "",
        },
        pluginsPath: {
            type: String,
            default: "",
        },
        langPath: {
            type: String,
        },
        minChangeSize: {
            type: Number,
            default: 5,
        },
        loadStyleMode: {
            type: Boolean,
            default: true,
        },
        urlType: {
            type: String,
            default: "",
        },
        newlineTag: {
            type: String,
            default: "p",
        },
        pasteType: {
            type: Number,
            default: 2,
        },
        dialogAlignType: {
            type: String,
            default: "page",
        },
        shadowMode: {
            type: Boolean,
            default: true,
        },
        zIndex: {
            type: Number,
            default: 811213,
        },
        useContextmenu: {
            type: Boolean,
            default: true,
        },
        syncType: {
            type: String,
            default: "form",
        },
        indentChar: {
            type: String,
            default: "\t",
        },
        cssPath: {
            type: [String, Array],
        },
        cssData: {
            type: String,
        },
        bodyClass: {
            type: String,
            default: "ke-content",
        },
        colorTable: {
            type: Array,
        },
        afterCreate: {
            type: Function,
        },
        afterChange: {
            type: Function,
        },
        afterTab: {
            type: Function,
        },
        afterFocus: {
            type: Function,
        },
        afterBlur: {
            type: Function,
        },
        afterUpload: {
            type: Function,
        },
        uploadJson: {
            //
            type: String,
        },
        fileManagerJson: {
            //
            type: String,
        },
        allowPreviewEmoticons: {
            type: Boolean,
            default: true,
        },
        allowImageUpload: {
            type: Boolean,
            default: true,
        },
        allowFlashUpload: {
            type: Boolean,
            default: true,
        },
        allowMediaUpload: {
            type: Boolean,
            default: true,
        },
        allowFileUpload: {
            type: Boolean,
            default: true,
        },
        allowFileManager: {
            type: Boolean,
            default: false,
        },
        fontSizeTable: {
            type: Array,
            default: function () {
                return [
                    "9px",
                    "10px",
                    "12px",
                    "14px",
                    "16px",
                    "18px",
                    "24px",
                    "32px",
                ];
            },
        },
        imageTabIndex: {
            type: Number,
            default: 0,
        },
        formatUploadUrl: {
            type: Boolean,
            default: true,
        },
        fullscreenShortcut: {
            type: Boolean,
            default: false,
        },
        extraFileUploadParams: {
            type: Object,
            default: function () {
                return {};
            },
        },
        filePostName: {
            type: String,
            default: "imgFile",
        },
        fillDescAfterUploadImage: {
            type: Boolean,
            default: false,
        },
        afterSelectFile: {
            // 
            type: Function,
        },
        pagebreakHtml: {
            type: String,
            default:
                '<hr style="page-break-after: always;" class="ke-pagebreak" />',
        },
        allowImageRemote: {
            type: Boolean,
            default: true,
        },
        autoHeightMode: {
            type: Boolean,
            default: true,
        },
        fixToolBar: {
            type: Boolean,
            default: false,
        },
        tabIndex: {
            type: Number,
        },
    },
    watch: {
        content(val) {
            this.editor && val !== this.outContent && this.editor.html(val);
        },
        outContent(val) {
            this.$emit("update:content", val);
            this.$emit("on-content-change", val);
        },
    },
    mounted() {
        // 
        this.initEditor();
    },
    /**
     * keep-alive activated deactivated
     *  createdmounted deactivated  activated
     */
    activated() {
        // keep-alive 
        this.initEditor();
    },
    deactivated() {
        // keep-alive 
        this.removeEditor();
    },
    methods: {
        
        uploadSuccess(res) {
            let url = this.imgBaseUrl + res.id;
            // 
            this.editor.insertHtml(`<img src="${url}" />`);
        },
        removeEditor() {
            window.KindEditor.remove("#" + this.id);
        },
        initEditor() {
            var _this = this;
            _this.removeEditor();
            _this.editor = window.KindEditor.create("#" + this.id, {
                readonlyMode: _this.readonlyMode,
                header: _this.header,
                width: _this.width,
                height: _this.height,
                minWidth: _this.minWidth,
                minHeight: _this.minHeight,
                items: _this.items,
                noDisableItems: _this.noDisableItems,
                filterMode: _this.filterMode,
                htmlTags: _this.htmlTags,
                wellFormatMode: _this.wellFormatMode,
                resizeType: _this.resizeType,
                themeType: _this.themeType,
                langType: _this.langType,
                designMode: _this.designMode,
                fullscreenMode: _this.fullscreenMode,
                basePath: _this.basePath,
                themesPath: _this.themesPath,
                pluginsPath: _this.pluginsPath,
                langPath: _this.langPath,
                minChangeSize: _this.minChangeSize,
                loadStyleMode: _this.loadStyleMode,
                urlType: _this.urlType,
                newlineTag: _this.newlineTag,
                pasteType: _this.pasteType,
                dialogAlignType: _this.dialogAlignType,
                shadowMode: _this.shadowMode,
                zIndex: _this.zIndex,
                useContextmenu: _this.useContextmenu,
                syncType: _this.syncType,
                indentChar: _this.indentChar,
                cssPath: _this.cssPath,
                cssData: _this.cssData,
                bodyClass: _this.bodyClass,
                colorTable: _this.colorTable,
                afterCreate: function () {
                    this.clickToolbar("image", function () {
                        if (
                            _this.maxlength >= 0 &&
                            _this.contentLength < _this.maxlength
                        ) {
                            _this.$refs.srbigupload.$children[0].$children[0].$el.click();
                        }
                        this.hideDialog();
                    });
                },
                afterChange: function () {
                    _this.outContent = this.html();
                    _this.contentLength = this.count("text");
                    _this.$emit("onEditorChange");
                },
                afterTab: _this.afterTab,
                afterFocus: _this.afterFocus,
                afterBlur: function () {
                    _this.$emit("onEditorBlur");
                },
                afterUpload: _this.afterUpload,
                uploadJson: _this.uploadJson,
                fileManagerJson: _this.fileManagerJson,
                allowPreviewEmoticons: _this.allowPreviewEmoticons,
                allowImageUpload: _this.allowImageUpload,
                allowFlashUpload: _this.allowFlashUpload,
                allowMediaUpload: _this.allowMediaUpload,
                allowFileUpload: _this.allowFileUpload,
                allowFileManager: _this.allowFileManager,
                fontSizeTable: _this.fontSizeTable,
                imageTabIndex: _this.imageTabIndex,
                formatUploadUrl: _this.formatUploadUrl,
                fullscreenShortcut: _this.fullscreenShortcut,
                extraFileUploadParams: _this.extraFileUploadParams,
                filePostName: _this.filePostName,
                fillDescAfterUploadImage: _this.fillDescAfterUploadImage,
                afterSelectFile: _this.afterSelectFile,
                pagebreakHtml: _this.pagebreakHtml,
                allowImageRemote: _this.allowImageRemote,
                autoHeightMode: _this.autoHeightMode,
                fixToolBar: _this.fixToolBar,
                tabIndex: _this.tabIndex,
            });
        },
    },
};
</script>



