<template>
    <div class="sr-step">
        <ul :class="{'sr-step-box': true, 'sr-step-white': mode === 'white'}">
            <li
                v-for="(item, index) in value"
                :key="index"
                :class="{
                    'sr-step-item': true,
                    checked_node: item.status == '2',
                    wait_node: item.status == '1',
                }"
            >
                <div class="sr-step-head">
                    <div class="line"></div>
                    <div class="node"></div>
                    <img
                        :src="current_node1"
                        class="current_node"
                        v-if="mode === 'blue' && item.status == '1'"
                    />
                    <img
                        :src="current_node2"
                        class="current_node"
                        v-if="mode === 'white' && item.status == '1'"
                    />
                </div>
                <div class="node-name">
                    {{ item.nodeName }}
                    <el-popover
                        placement="bottom"
                        width="136"
                        popper-class="step_popover"
                        trigger="hover"
                        v-if="Array.isArray(item) && item.length"
                    >
                        <el-table
                            :show-header="false"
                            :data="item"
                            style="width: 100%"
                        >
                            <el-table-column prop="nodeName"></el-table-column>
                        </el-table>
                        <i class="el-icon-more" slot="reference"></i>
                    </el-popover>
                    <i class="el-icon-more" slot="reference" v-if="Array.isArray(item) && !item.length"></i>
                </div>
            </li>
        </ul>
    </div>
</template>


<script>
import current_node1 from "./current_node1.png";
import current_node2 from "./current_node2.png";

export default {
    name: "SrStep",
    model: {
        prop: "value",
        event: "change",
    },
    data() {
        return {
            current_node1,
            current_node2,
        };
    },
    props: {
        value: {
            type: Array,
            default: () => {
                return [];
            },
        },
        mode: {
            type: String,
            default: () => {
                return "blue";
            },
        },
    },
    mounted() {},
};
</script>

<style scoped>
</style>



