<template>
    <div class="sr-text">
        <el-popover
            :placement="item.placement || 'top-start'"
            :width="item.width || 400"
            :trigger="item.trigger || 'click'"
			popper-class="sr-text-popover"
        >
            <el-input type="textarea" resize="none" :rows="5" :placeholder="item.placeholder" v-model="value"></el-input>
            <el-input v-model="value" placeholder="" slot="reference"></el-input>
        </el-popover>
    </div>
</template>

<script>
export default {
    name: "SrText",
    model: {
        prop: "value",
        event: "change",
    },
    props: ["value", "item"],
};
</script>

<style>
</style>


