<template>
  <div>
    <el-input-number
      :disabled="item.disabled"
      :value="value"
      :controls-position="item.cposition?item.cposition:'right'"
      :placeholder="item.placeholder"
      :min="item.min ? item.min : -100"
      :max="item.max ? item.max : 100"
      clearable
      @change="change($event)"
      @input="change($event)"
      class="sr-number"
    ></el-input-number>
  </div>
</template>

<!--
  
  
  20201119 16:56:33
  v1.0
-->
<script>
export default {
  name: "SrNumber",
  model: {
    prop: "value",
    event: "change",
  },

  props: {
    value: String | Number,
    item: {
      type: Object,
      default: {},
    },
  },
  data() {
    return {};
  },
  methods: {
    change(val) {
      this.$emit("change", val);
    },
  },
};
</script>

<style scoped >
</style>



