<template>
  <div class="sr-dialog-select">
    <el-input
      v-model="project.projectName"
      readonly
      placeholder=""
      suffix-icon="el-icon-caret-bottom"
      @focus="handleFocus"
    ></el-input>
    <el-dialog
      :title="dialogTitle"
      :visible.sync="dialogVisible"
      append-to-body
      :width="dialogWidth"
      center
      custom-class="dialog_select"
      v-if="dialogVisible"
      :before-close="handleClose"
    >
      <div class="dialog_select_header">
        <div class="search">
          <slot name="search">
            <el-input
              v-model="keyword"
              placeholder="/"
            ></el-input>
          </slot>
          <span class="buttonBox" v-if="buttonFlag">
            <sr-popover content="">
              <el-button
                size="small"
                type="primary"
                icon="el-icon-search"
                circle
                @click="search"
              ></el-button>
            </sr-popover>
            <sr-popover content="">
              <el-button
                size="small"
                icon="el-icon-refresh"
                circle
                @click="refresh"
              ></el-button>
            </sr-popover>
          </span>
        </div>
      </div>
      <SrTable
        :data="table.data"
        :config="table.tableConfig"
        :page="table.page"
        @pageChanged="pageChanged"
        @clickRow="clickRow"
        @rowDblclick="rowDblclick"
        @selected="selected"
        @selectAll="selected"
      />
      <div slot="footer" class="dialog-footer" v-if="footerFlag">
        <el-button size="small" round type="primary" @click="confirm"
          ></el-button
        >
        <el-button size="small" round @click="close"></el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
export default {
  name: "SrDialogSelect",
  props: {
    buttonFlag: {
      type: Boolean,
      default() {
        return true;
      }
    },
    footerFlag: {
      type: Boolean,
      default() {
        return true;
      }
    },
    dialogTitle: {
      type: String,
      default: ""
    },
    dialogWidth: {
      type: String,
      default: () => {
        return "800px";
      }
    },
    getList: {
      type: Function,
      default: () => {
        return function() {};
      }
    },
    // 
    params: Object,
    hasChildrenFlag: {
      type: Boolean,
      default() {
        return true;
      }
    },
    tableConfig: {
      type: Object,
      default: () => {
        return {};
      }
    },
    // 
    selectData: {
      type: Array,
      default: () => {
        return [];
      }
    },
    // 
    max: {
      type: Number,
      default: () => {
        return 20;
      }
    },
    header: {
      type: Array,
      default: () => {
        return [
          {
            label: "",
            prop: "projectName"
          },
          {
            label: "",
            prop: "projectCode"
          }
        ];
      }
    },
    isExpert: {
      type: Boolean,
      default: () => {
        return false;
      }
    },
    selectionConfig: {
      type: Object,
      default: () => {
        return {
          display: false
        };
      }
    }
  },
  data() {
    return {
      dialogVisible: false,
      project: {
        projectName: null,
        projectCode: null,
        projectId: null
      },
      keyword: null,
      table: {
        data: [],
        tableConfig: {
          load: this.load,
          highlightCurrentRow: true,
          selectionConfig: this.selectionConfig,
          header: this.header,
          indexConfig: {
            display: true,
            label: "",
            width: "52"
          }
        },
        page: {
          // 
          total: 0, //
          pages: 0, //
          pageSize: 10, //
          pageNum: 1, //
          layout: "prev, pager, next, jumper"
        }
      },
      selectObj: null
    };
  },
  mounted() {
    Object.keys(this.tableConfig).forEach(item => {
      this.$set(this.table.tableConfig, item, this.tableConfig[item]);
    });
  },
  methods: {
    close() {
      this.$emit("close", "");
      this.dialogVisible = false;
    },
    handleClose(done) {
      this.$emit("close", "");
      done();
    },
    load(tree, treeNode, resolve) {
      let params = {
        pageNum: 1,
        pageSize: 999,
        isSubproject: 1,
        mainProjectId: tree.id
      };
      this.getList(params).then(res => {
        if (res.msg.code === "0000") {
          resolve(res.data.records);
        }
      });
    },
    search() {
      this.table.page.pageNum = 1;
      this.queryList();
    },
    // 
    refresh() {
      this.keyword = null;
      this.table.page.pageNum = 1;
      this.queryList();
    },
    // dialog
    handleFocus() {
      this.dialogVisible = true;
      this.queryList();
    },
    // 
    clickRow(val) {
      if (this.table.tableConfig.highlightCurrentRow) {
        this.selectObj = val;
      }
    },
    // 
    rowDblclick(val) {
      if (this.table.tableConfig.highlightCurrentRow) {
        this.selectObj = val;
        this.confirm();
      }
    },
    //
    pageChanged(val) {
      this.table.page.pageNum = val;
      this.queryList();
    },
    // 
    queryList() {
      this.$emit("queryList");
      let params = {
        pageNum: this.table.page.pageNum,
        pageSize: this.isExpert ? 999 : 10,
        keyword: this.keyword,
        ...this.params
      };
      this.getList(params).then(res => {
        if (res.msg.code === "0000") {
          this.table.data = res.data.records.map(item => {
            if (!this.hasChildrenFlag) {
              item.hasChildren = false;
            }
            return item;
          });
          this.table.page.total = res.data.total;
        }
      });
    },
    // 
    confirm() {
      if (this.table.tableConfig.selectionConfig.display) {
        // 
        if (this.selectObj) {
          if (this.selectObj.length + this.selectData.length > this.max) {
            this.$message({
              message: "",
              type: "warning"
            });
          } else {
            this.project = this.selectObj;
            this.$emit("select", this.project);
            this.dialogVisible = false;
          }
        } else {
          this.dialogVisible = false;
        }
      } else {
        // 
        this.project = this.selectObj;
        this.$emit("select", this.project);
        this.dialogVisible = false;
      }
    },
    // 
    selected(val) {
      this.selectObj = val;
    }
  }
};
</script>



