// code
function checkCodeInArr(code, arr) {
  arr = arr || sessionStorage.getItem("permission_btns") || [];
  if (!code) {
    return true;
  }
  arr = typeof arr == "string" ? JSON.parse(arr) : arr;
  let flag = false;
  for (let k = 0; k < arr.length; k++) {
    if (arr[k].permissionCode == code) {
      flag = true;
      break;
    }
  }

  return flag;
}

function merge(colArr, list) {
  var allProps = [];
  colArr.forEach((props, index) => {
    allProps.push(props);
    list = getLevel(props, allProps, list);
  });
  return list;
};

//
function getLevel(props, allProps, list) {
  // rowkey
  var rowKey = {};
  for (var item of list) {
    // 
    let propInfo = getPropStr(allProps, item);

    if (rowKey[propInfo.key]) {
      // push
      rowKey[propInfo.key].children.push(item);
    } else {
      // rowKey
      let obj = propInfo;
      obj.children = [item];
      rowKey[propInfo.key] = obj;
    }
  }
  let info = [];
  for (var key in rowKey) {
    rowKey[key].children.forEach((item, index) => {
      // children
      if (index == 0) {
        item = Object.assign(
          item,
          getMergeInfo(props, rowKey[key].children.length)
        );
      } else {
        item = Object.assign(item, getMergeInfo(props, 0));
      }
      info.push(item);
    });
  }
  // 
  return info;
}

//
function getMergeInfo(props, num) {
  let obj = {};
  obj[props + "_merge"] = {
    rowspan: num,
    colspan: 1
  };
  return obj;
}
// ,
/**
 *
 * @param {label} props
 * @param {} info
 * @returns {key: '', classification: ''}
 */
function getPropStr(props, info) {
  let obj = {};
  let propStr = props.map(item => {
    obj[item] = info[item];
    return info[item];
  });
  obj.key = propStr.toString();
  return obj;
}

function routeClose(str, setState,timeout) {
  if (!str) return;
  var routerBase = "/" + str
  let msg = {
    title: this.$route.meta.title,
    url: `${routerBase}/#${this.$route.fullPath}`,
    meta: this.$route.meta,
    timeout: timeout||0
  }
  setTimeout(() => {
    setState({
      ignore: "closeAddTab",
      msg
    });
  })
}
/**
 * 
 * @param {Object}
 * params.content 
 * params.title 
 * params.okFun 
 * params.cancelFun 
 */
function showConfirmMessage(params) {
  let content = params ? params.content : ''
  let title = params ? params.title : ''
  this.$confirm(content, title, {
    type: 'warning',
    center: true,
    showClose: false,
    showCancelButton: true, // 
    showConfirmButton: true, // 
    cancelButtonText: "", // 
    confirmButtonText: "", // 
    customClass: "changsha_confirm" //  
  }).then(() => {
    if (params && params.okFun) {
      params.okFun()
    }
  }).catch(() => {
    if (params && params.cancelFun) {
      params.cancelFun()
    }
  })
}

export default {
  checkCodeInArr,
  merge,
  routeClose,
  showConfirmMessage
};



