<template>
    <div :class="`basicInfo sr-view ${seniorFrom ? 'seniorFromView' : ''}`">
        <el-row class="row_box">
            <template v-for="(item, index) in formDataCopy">
                <el-col
                    :key="index"
                    v-if="item.type == 'title' && !item.isHide"
                    :span="item.span || item.col"
                    :class="item.bgColor ? 'formViewBg' : ''"
                >
                    <div :style="item.style" class="title">
                        {{ item.value || "-" }}
                    </div>
                </el-col>
                <el-col
                    :key="index"
                    :span="item.span || item.col"
                    v-if="!item.type"
                    v-show="!item.hidden"
                    :class="item.bgColor ? 'formViewBg' : ''"
                >
                    <label class="label-item ellipsis">{{ item.label }}</label>
                    <span
                        class="overflow context"
                        v-if="
                            ruleFormCopy[item.value] &&
                            ruleFormCopy[item.value].length > 156
                        "
                    >
                        <span
                            :title="ruleFormCopy[item.value]"
                            v-show="!item.operateFlag"
                            >{{
                                ruleFormCopy[item.value].slice(0, 150) + "..."
                            }}</span
                        >
                        <span
                            :title="ruleFormCopy[item.value]"
                            v-show="item.operateFlag"
                            >{{ formatContext(item) }}</span
                        >
                    </span>
                    <template v-else>
                        <el-tooltip
                            popper-class="selfTip"
                            v-if="item.showTip"
                            class="item"
                            effect="light"
                            :content="formatContext(item)"
                            placement="top"
                        >
                            <span class="context">{{
                                formatContext(item)
                            }}</span>
                        </el-tooltip>

                        <el-tooltip
                            popper-class="selfTip"
                            v-else-if="formatContext(item).length >= 24"
                            class="item"
                            effect="light"
                            :content="formatContext(item)"
                            placement="top"
                        >
                            <span class="context">{{
                                formatContext(item)
                            }}</span>
                        </el-tooltip>
                        <span class="context" v-else>{{
                            formatContext(item)
                        }}</span>
                    </template>
                </el-col>

                <!-- value -->
                <el-col
                    :key="index"
                    :span="item.span || item.col"
                    v-if="item.type == 'value' && !item.isHide"
                    v-show="!item.hidden"
                    :class="item.bgColor ? 'value_box formViewBg' : 'value_box'"
                >
                    <label class="label-item ellipsis">{{ item.label }}</label>
                    <span
                        :class="[
                            'overflow',
                            'context',
                            item.class,
                            item.className,
                        ]"
                    >
                        <slot
                            v-if="item.valueSlot ? true : false"
                            :name="item.value"
                            :value="ruleFormCopy[item.value]"
                        ></slot>
                        <span v-else>{{ formatContext(item) }}</span>
                    </span>
                </el-col>
                <!-- airBox -->
                <el-col
                    :key="index"
                    :span="item.span ? item.span : 24"
                    v-if="item.type == 'airBox'"
                    :class="
                        item.bgColor ? 'formViewBg airBox_box' : 'airBox_box'
                    "
                >
                    <slot
                        v-if="item.airBoxSlot ? item.airBoxSlot : false"
                        :name="item.id"
                    ></slot>
                </el-col>

                <!-- autoValue -->
                <el-col
                    :key="index"
                    :span="item.span || item.col"
                    v-if="item.type == 'autoValue'"
                    v-show="!item.hidden"
                    class="autoValue_box"
                    :class="
                        item.bgColor
                            ? 'formViewBg autoValue_box'
                            : 'autoValue_box'
                    "
                >
                    <label class="label-item ellipsis">{{ item.label }}</label>
                    <el-popover
                        placement="top-start"
                        trigger="hover"
                        :content="formatContext(item)"
                        class="value_popover"
                    >
                        <template slot="reference">
                            <div class="context autoValue">
                                <span>{{ formatContext(item) }}</span>
                            </div>
                        </template>
                    </el-popover>
                </el-col>
                <!-- editor -->
                <el-col
                    :key="index"
                    :span="item.span || item.col"
                    v-if="item.type == 'editor'"
                    class="editor_box"
                    :class="
                        item.bgColor ? 'formViewBg editor_box' : 'editor_box'
                    "
                >
                    <label class="label-item ellipsis">{{ item.label }}</label>
                    <SrEditor
                        :maxlength="item.maxlength ? item.maxlength : 0"
                        :showBtn="item.showBtn"
                        :type="item.readonly"
                        ref="SrEditor"
                        :placeholder="item.placeholder"
                        :value="ruleFormCopy[item.value]"
                    />
                </el-col>
                <!--uploader -->
                <el-col
                    :key="index"
                    :span="item.span || 24"
                    v-else-if="
                        item.type &&
                        item.type.startsWith('uploader') &&
                        !item.isHide
                    "
                    :class="
                        item.bgColor
                            ? 'formViewBg html_box'
                            : 'html_box'
                    "
                >
                    <UploaderView
                        :data-up="item"
                        :view-data="ruleFormCopy[item.value]"
                        :label-width="220"
                    ></UploaderView>
                </el-col>
                <!-- html -->
                <el-col
                    :key="index"
                    :span="item.span || 24"
                    v-else-if="item.type === 'html' && !item.isHide"
                    :class="item.bgColor ? 'formViewBg html_box' : 'html_box'"
                >
                    <el-row>
                        <el-col :span="4">
                            <label class="label-item ellipsis">{{
                                item.label
                            }}</label>
                        </el-col>
                        <el-col :span="20">
                            <SrMultipleText
                                v-model="ruleFormCopy[item.value]"
                                :item="item"
                                ref="SrMultipleText"
                                v-if="ruleFormCopy[item.value] && item.textarea"
                            ></SrMultipleText>
                            <div
                                class="context"
                                v-else-if="ruleFormCopy[item.value]"
                            ></div>
                            <div class="context" v-else>-</div>
                        </el-col>
                    </el-row>
                </el-col>
                <!--  -->
                <el-col
                    :key="index"
                    :span="item.span || item.col"
                    v-else-if="item.type === 'star'"
                    class="star_box"
                    :class="item.bgColor ? 'formViewBg star_box' : 'star_box'"
                >
                    <label class="label-item ellipsis">{{ item.label }}</label>
                    <div class="context" @click="sendEvent(item.eventName)">
                        <el-rate
                            :value="ruleFormCopy[item.value]"
                            disabled="disabled"
                            :show-score="item.showScore || false"
                            :score-template="ruleFormCopy[item.value] + ''"
                            text-color="#ff9900"
                        ></el-rate>
                    </div>
                </el-col>
            </template>
        </el-row>
    </div>
</template>

<script>
import UploaderView from "./uploaderView";
export default {
    components: {
        UploaderView,
    },
    inject: ["baseUrl"],
    name: "SrView",
    props: {
        stringConfig: String,
        formData: {
            type: Array,
            default: () => {
                return [];
            },
        },
        ruleForm: {
            type: Object,
            default: () => {
                return {};
            },
        },
        seniorFrom: {
            type: Boolean,
            default: () => {
                return false;
            },
        },
        enums: Object,
        hideColon: Boolean, // 
        emptyPlacehoder: {
            type: String,
            default: () => {
                // - -
                return "-";
            },
        },
    },
    data() {
        return {
            operateFlag: false,
            bgArr: [],
            expand: false,
            formDataCopy: [],
            ruleFormCopy: {},
        };
    },
    mounted() {
        this.$set(
            this,
            "formDataCopy",
            JSON.parse(JSON.stringify(this.formData))
        );
        this.$set(
            this,
            "ruleFormCopy",
            JSON.parse(JSON.stringify(this.ruleForm))
        );
    },
    watch: {
        formData: {
            handler: function (val, old) {
                this.$set(
                    this,
                    "formDataCopy",
                    JSON.parse(JSON.stringify(val))
                );
            },
            deep: true,
        },
        stringConfig(val) {
            this.$set(this, "formDataCopy", JSON.parse(val));
        },
        ruleForm: {
            handler: function (val) {
                this.$set(
                    this,
                    "ruleFormCopy",
                    JSON.parse(JSON.stringify(val))
                );
            },
            deep: true,
        },
    },
    methods: {
        setBoxHeight(index, item) {
            if (
                this.formDataCopy[index - 1] &&
                item.type == this.formDataCopy[index - 1].type
            ) {
                return "8px";
            } else {
                return "16px";
            }
        },
        getWidth(item) {
            return {
                width:
                    "calc(100% - " +
                    (item.labelWidth ? item.labelWidth + 25 : 100) +
                    "px)",
            };
        },
        /**
         * 
         */
        sendEvent(eventName) {
            this.$emit("eventNotify", eventName);
        },
        format(num) {
            return num;
        },
        formatContext(item) {
            if ([null, '', undefined].includes(this.ruleFormCopy[item.value])) {
                return this.emptyPlacehoder
            } else {
                return this.ruleFormCopy[item.value]
            }
        },
        operateClick(item) {
            this.$set(item, "operateFlag", !item.operateFlag);
        },
        getLabel(value, list = [], selectName, selectValue) {
            if (value || value === 0) {
                const found = list.find((item) => {
                    return (
                        (selectValue ? item[selectValue] : item[dataValue]) ==
                        value
                    );
                });
                return found
                    ? selectName
                        ? found[selectName]
                        : found[dataName]
                    : "";
            }
            return "";
        },
    },
};
</script>


