<template>
  <el-date-picker
    class="sr-date-time"
    :type="item.timeType ? item.timeType : 'datetime'"
    :disabled="item.disabled"
    v-model="date"
    :placeholder="item.placeholder"
    start-placeholder=""
    end-placeholder=""
    :clearable="false"
    @change="
      (val) => {
        change(val);
      }
    "
    :format="item.format ? item.format : 'yyyy-MM-dd HH:mm:ss'"
    :value-format="item.format ? item.format : 'yyyy-MM-dd HH:mm:ss'"
    :range-separator="item.range ? item.range : ' - '"
    :picker-options="item.pickerOptions ? item.pickerOptions : {}"
  ></el-date-picker>
</template>

<!--
  
  
  20201119 17:09:02
  v1.0
-->
<script>
export default {
  name: "SrDatetime",
  model: {
    prop: "value",
    event: "change",
  },
  props: {
    value: String | Number,
    item: {
      type: Object | Array,
      default: {},
    },
  },
  data() {
    return {
      date: this.value,
    };
  },
  watch: {
    value: {
      handler(val) {
        this.date = val;
      },
      deep: true,
    },
  },
  methods: {
    change(value) {
      
      this.$emit("change", value);
    },
    /**
     * 
     */
    disabledDate() {
      return false;
    },
  },
};
</script>



