<template>
    <section class="form-config sr-add">
        <el-form
            :ref="refForm"
            :rules="rules"
            :model="form"
            label-suffix=""
            :disabled="isDisabledAll"
            :label-width="(otherConfig.labelNum || labelNum) + 'px'"
            :validate-on-rule-change="otherConfig.validateOnRuleChange || false"
        >
            <template v-for="(item, index) in config">
                <el-form-item
                    :key="'fa-item-' + index"
                    v-if="!item.isHide"
                    :label="item.name"
                    :prop="item.bind"
                    :class="[
            'col-' + (item.col ? item.col : 12),
            { textarea_vertical: item.type == 'auto_textarea' },
            'relative',
            'border-box',
            item.className,
            { rightSlot: item.rightSlot },
            { closePadding: item.closePadding },
          ]"
                    :style="'display:' + item.display"
                >
                    <template v-if="item.type === 'upload' && item.fileTip" slot="label">
                        <el-popover
                            placement="top-start"
                            width="480"
                            trigger="hover"
                            :content="item.fileTip"
                        >
                            <i class="sr-icon-prompt-fill" slot="reference"></i>
                        </el-popover>
                        {{ item.name }}
                    </template>
                    <!--     -->
                    <div v-if="item.type == 'switch'">
                        <el-switch
                            v-model="form[item.bind]"
                            v-bind="item"
                            @change="
                (val) => {
                  item.change && item.change(val, item);
                }
              "
                        ></el-switch>
                    </div>

                    <!--       -->
                    <div v-if="item.type == 'radio'">
                        <el-radio-group
                            v-model="form[item.bind]"
                            @change="
                (val) => {
                  item.change && item.change(val, item);
                }
              "
                        >
                            <el-radio
                                v-for="op in enums[item.dataList]"
                                :label="item.label ? op[item.label] : op.dataValue"
                                :name="item.label ? op[item.label] : op.dataValue"
                                v-bind="item"
                                :key="op.dataValue"
                            >{{ op.dataName }}</el-radio>
                        </el-radio-group>
                    </div>

                    <!--       -->
                    <div v-if="item.type == 'checkbox'">
                        <el-checkbox-group
                            v-model="form[item.bind]"
                            :size="item.size"
                            :disabled="item.disabled"
                            @change="
                (val) => {
                  item.change && item.change(val, item);
                }
              "
                        >
                            <el-checkbox
                                v-for="op in enums[item.dataList]"
                                :label="item.label ? op[item.label] : op.dataValue"
                                :name="item.label ? op[item.label] : op.dataValue"
                                :key="op.dataValue"
                                v-bind="item"
                            >{{ item.value ? op[item.value] : op.dataName }}</el-checkbox>
                        </el-checkbox-group>
                    </div>

                    <!--    -->
                    <div class="airBox" v-if="item.type == 'airBox'">
                        <slot v-if="item.airBoxSlot ? item.airBoxSlot : false" :name="item.id"></slot>
                    </div>

                    <!--   input   -->
                    <div v-else-if="item.type == 'input'">
                        <el-input
                            v-model.trim="form[item.bind]"
                            v-bind="item"
                            :required="false"
                            @change="
                (val) => {
                  item.focus && item.focus(val, item);
                }
              "
                            @focus="
                (val) => {
                  item.focus && item.focus(val, item);
                }
              "
                        >
                            <el-button
                                :slot="item.append ? 'append' : false"
                                :icon="item.icon ? item.icon : 'el-icon-search'"
                                @click="callFn(item, $event)"
                            ></el-button>
                        </el-input>
                    </div>
                    <!--   text   -->
                    <div v-else-if="item.type == 'text'">
                        <SrText v-model.trim="form[item.bind]" :item="item"></SrText>
                    </div>
                    <!--   input -->
                    <div v-else-if="item.type == 'number'">
                        <el-input-number
                            v-bind="item"
                            v-model.number="form[item.bind]"
                            :controls-position="item['controls-position'] || ''"
                        ></el-input-number>
                    </div>

                    <!--   select   -->
                    <div v-else-if="item.type == 'select'">
                        <el-select
                            v-model="form[item.bind]"
                            v-bind="item"
                            clearable
                            @change="
                (val) => {
                  item.change && item.change(val, item);
                }
              "
                        >
                            <el-option
                                v-for="(op, i) in enums[item.dataList]"
                                :key="i"
                                :disabled="op.disabled"
                                :label="item.label ? op[item.label] : op.dataName"
                                :value="item.value ? op[item.value] : op.dataValue"
                            ></el-option>
                        </el-select>
                    </div>

                    <!--   dialogselect   -->
                    <div v-else-if="item.type == 'dialogSelect'">
                        <sr-dialog-select
                            :getList="item.getList"
                            :params="item.params"
                            @select="
                (val) => {
                  item.select && item.select(val, item);
                }
              "
                        ></sr-dialog-select>
                    </div>

                    <!--   textarea  -->
                    <div v-else-if="item.type == 'textarea'">
                        <SrTextarea
                            v-model.trim="form[item.bind]"
                            @change="callFn(item, $event)"
                            :item="item"
                        ></SrTextarea>
                    </div>

                    <!--      -->
                    <div v-else-if="item.type == 'editor'">
                        <sr-kind-editor
                            :id="'KindEditor' + item.bind"
                            :readonlyMode="item.readonly"
                            :pluginsPath="pluginsPath"
                            :placeholder="item.placeholder"
                            ref="KindEditor"
                            v-bind="item"
                            :content.sync="form[item.bind]"
                        />
                    </div>

                    <!--   dete    -->
                    <div v-else-if="item.type == 'dateSelect'">
                        <el-date-picker
                            v-bind="item"
                            v-model="form[item.bind]"
                            :type="item.timeType ? item.timeType : 'datetime'"
                            :format="item.format ? item.format : 'yyyy-MM-dd HH:mm:ss'"
                            :value-format="item.format ? item.format : 'yyyy-MM-dd HH:mm:ss'"
                            @change="
                                (val) => {
                                item.change && item.change(val, item);
                                }
                            "
                        ></el-date-picker>
                    </div>

                    <!--    -->
                    <div v-else-if="item.type == 'dateRelevancy'">
                        <SrDatePicker
                            class="relation"
                            v-model="form[item.bind]"
                            :startname="item.startname ? item.startname : false"
                            :endname="item.endname ? item.endname : false"
                            :item="item"
                            @changeModel="(val) => changeModel(val, item)"
                            @change="
                (val) => {
                  item.change && item.change(form[item.bind], item);
                }
              "
                        ></SrDatePicker>
                    </div>

                    <!--   upload -->
                    <div  v-if="item.type == 'upload'">
                        <SrBigUpload
                            ref="srUpload"
                            @uploadSuccess="
                (data) => {
                  BigFileUploadSuccess(data, item);
                }
              "
                            @resume="
                (data) => {
                  clearValidateItem(item);
                }
              "
                            @startUpload="
                (data) => {
                  clearValidateItem(item);
                }
              "
                            @download="
                (data) => {
                  this.download && this.download(data);
                }
              "
                            :item="item"
                            :form="form"
                            :fileList="
                form[item.bind] &&
                Array.isArray(form[item.bind]) &&
                form[item.bind].length
                  ? form[item.bind]
                  : item.fileList
              "
                        ></SrBigUpload>
                    </div>
                    <!--  -->
                    <SrUpload
                        v-if="item.type === 'uploadImg'"
                        v-model="form[item.bind]"
                        :item="item"
                    ></SrUpload>
                    <!-- -->
                    <el-cascader
                        v-if="item.type == 'cascader'"
                        style="width: 100%"
                        :options="enums[item.data]"
                        v-model="form[item.bind]"
                        filterable
                        @change="
                (val) => {
                  item.change && item.change(val, item);
                }
              "
                        v-bind="item"
                    ></el-cascader>

                    <sr-select-people
                        v-model="form[item.bind]"
                        v-bind="item"
                        @addPeople="() => {
                            item.addPeople && item.addPeople()
                        }"
                        @delPeople="() => {
                            item.delPeople && item.delPeople()
                        }"
                        v-if="item.type === 'selectPeople'"
                    ></sr-select-people>
                    
                    <sr-duration v-model="form[item.bind]" v-if="item.type === 'duration'"></sr-duration>
                    <sr-tag v-model="form[item.bind]" v-if="item.type ==='tag'"></sr-tag>

                    <!--   -->
                    <div v-if="item.type == 'star'" class="flexBox ver-middle">
                        <el-popover placement="bottom" trigger="hover" v-if="item.hover">
                            <div>
                                <div class="title">{{ item.hover }}</div>
                            </div>
                            <el-rate slot="reference" v-model="form[item.bind]" v-bind="item"></el-rate>
                        </el-popover>
                        <el-rate
                            v-else-if="item.scoreTemplateType"
                            slot="reference"
                            v-model="form[item.bind]"
                            v-bind="item"
                            @change="validatorField(item)"
                            :score-template="`${form[item.bind] * 2}`"
                        ></el-rate>
                        <el-rate
                            v-else
                            slot="reference"
                            v-model="form[item.bind]"
                            @change="validatorField(item)"
                            :score-template="form[item.bind] + ''"
                            v-bind="item"
                        ></el-rate>
                    </div>
                </el-form-item>
            </template>
            <el-form-item v-if="button && button.length" style="width: 100%; text-align: center">
                <el-button
                    v-for="(btn, index) in button"
                    :key="index"
                    :type="btn.type ? btn.type : ''"
                    @click="callFn(btn)"
                >{{ btn.name }}</el-button>
            </el-form-item>
        </el-form>
    </section>
</template>
<script>
import { getRules } from "./rules.js";
const ruleItem = getRules();
export default {
    name: "SrAdd",
    inject: ["baseUrl", "imgBaseUrl", "uploadBaseUrl"],
    props: {
        pickerOptions: {
            type: [Object],
        },
        otherConfig: {
            type: [Object],
            default() {
                return {
                    labelNum: 190,
                    column: 2,
                };
            },
        },
        config: {
            type: Array,
        },
        button: {
            type: Array,
        },
        form: {
            type: [Object, Array],
        },
        refForm: {
            type: String,
            default: "refForm",
        },
        enums: {
            type: Object,
        },
        column: {
            type: Number,
        },
        otherRule: {
            type: Object,
        },
        isDisabledAll: Boolean, // 
    },
    computed: {
        pluginsPath: function () {
            return window.location.origin + "/kindeditor/plugins/";
        },
    },
    data() {
        return {
            temporary: {}, // 
            fileList: [],
            rules: {},
            file: null,
        };
    },
    watch: {
        config: {
            handler(val) {
                if (val.length > 0) {
                    this.createRulesRequired();
                }
            },
            deep: true,
        },
    },
    mounted() {
        this.createRulesRequired();
        this.initTemporary();
    },
    methods: {
        //change
        changeModel(val, item) {
            if (!item.required) {
                return;
            }
            setTimeout(() => {
                this.form[item.bind] = val;
            }, 200);
        },
        // 
        clearValidateItem(item) {
            this.$refs[this.refForm].clearValidate([item.bind]);
        },
        /**
         * 
         */
        validatorField(item) {
            this.$refs[this.refForm].validateField(item.bind);
        },
        /**
         * 
         */
        initTemporary() {
            let obj = {};
            this.config.map((res) => {
                if (res.temporary) {
                    obj[res.temporary || res.bind] = "";
                }
            });
            this.temporary = obj;
        },
        /**
         * false
         */
        validate(callback) {
            let flag = false;
            let rs = null;
            this.$refs.srUpload &&
                this.$refs.srUpload.map((res) => {
                    if (res.uploader.isUploading()) {
                        this.$message.warning("!");
                        flag = true;
                        return false;
                    }
                });
            if (!flag) {
                this.$refs[this.refForm].validate((valid) => {
                    if (callback) {
                        callback(valid);
                    } else {
                        if (valid) {
                            let newPar = JSON.stringify(this.form).replace(
                                new RegExp('\\\\"', "gm"),
                                "'"
                            );
                            rs = JSON.parse(newPar);
                        }
                    }
                });
                return rs;
            }
        },
        /**
         * 
         */
        createRulesRequired() {
            let obj = {};
            this.config.map((res) => {
                // 
                if (res.bind) {
                    let arr = res.required
                        ? [Object.assign({}, ruleItem.requiredText[0])]
                        : [];
                    if (res.required) {
                        if (res.trigger) {
                            arr[0].trigger = res.trigger;
                        }
                        if (arr[0].required) {
                            arr[0].message = res.required;
                        }
                    }
                    obj[res.bind] = arr;
                }
                if (res.type === 'textarea' && res.maxlength) {
                    if (res.rules) {
                        res.rules[`maxLength_${res.maxlength}`] = `${res.maxlength}`
                    } else {
                        res.rules = {
                            [`maxLength_${res.maxlength}`]: `${res.maxlength}`
                        }
                    }
                }
            });
            this.otherRules(obj);
        },
        /**
         * 
         * @param {Object} obj 
         */
        otherRules(obj) {
            this.config.map((res) => {
                if (res.rules) {
                    // 
                    for (let key in res.rules) {
                        if (key.indexOf("maxLength") != -1) {
                            // 
                            let validator = Object.assign(
                                {},
                                ruleItem["maxLength"][0]
                            );
                            let arr = [validator];
                            arr[0].message = res.rules[key];
                            arr[0].trigger = res.trigger || arr[0].trigger;
                            arr[0].max = key.split("maxLength_")[1] - 0;
                            obj[res.bind] = [...obj[res.bind], ...arr];
                        } else if (key.indexOf("regular") != -1) {
                            // 
                            let validator = Object.assign(
                                {},
                                ruleItem["regular"][0]
                            );
                            let arr = [validator];
                            arr[0].message = res.rules[key];
                            arr[0].trigger = res.trigger || arr[0].trigger;
                            arr[0].pattern =
                                ruleItem.regularList[key.split("regular_")[1]];
                            obj[res.bind] = [...obj[res.bind], ...arr];
                        } else if (key.indexOf("validator") != -1) {
                            // 
                            let validator = Object.assign(
                                {},
                                ruleItem["validator"][0]
                            );
                            let arr = [validator];
                            arr[0].trigger = res.trigger || arr[0].trigger;
                            arr[0].validator = res.rules[key];
                            obj[res.bind] = [...obj[res.bind], ...arr];
                        } else if (key.indexOf("define") != -1) {
                            // 
                            let validator = Object.assign(
                                {},
                                ruleItem["define"][0]
                            );
                            let arr = [validator];
                            arr[0].message = res.rules[key].message;
                            arr[0].trigger =
                                res.rules[key].trigger || arr[0].trigger;
                            if (typeof res.rules[key].pattern == "string") {
                                arr[0].pattern = eval(res.rules[key].pattern);
                            } else {
                                arr[0].pattern = res.rules[key].pattern;
                            }

                            obj[res.bind] = [...obj[res.bind], ...arr];
                        } else {
                            // rule.js
                            let item = Object.assign({}, ruleItem[key][0]);
                            let arr = [];
                            arr.push(item);
                            arr[0].message = res.rules[key];
                            arr[0].trigger = res.trigger || arr[0].trigger;
                            obj[res.bind] = [...obj[res.bind], ...arr];
                        }
                    }
                }
            });

            this.rules = obj;
        },
        /**
         * 
         */
        validator(rule, value, callback) {
            if (value - 500 > 0) {
                callback(new Error(""));
            } else {
                callback();
            }
        },
        /**
         * 
         */
        disabledDate() {
        return false;
        },
        resetForm() {
            //
            this.$refs[this.refForm].resetFields();
        },

        // 
        BigFileUploadSuccess(fileList, item) {
            this.$set(this.form, item.bind, fileList);
            this.$set(item, "fileList", fileList);
        },

        callFn(btn, $event) {
            //
            if (btn.method && btn.method.constructor == Function) {
                // fn
                btn.method(btn, this.form);
                return;
            }
            let sum = 0;
            let obj = this.getDeepMethod(this.$parent, btn.method, sum, 10);
            if (obj.parent && obj.method) {
                obj.method.call(obj.parent, $event, this.form, this.config);
            }
        },
        // method
        getDeepMethod(parent, method, sum, limit) {
            sum++;
            if (!parent) {
                return {};
            } else if (sum == limit) {
                return {};
            } else if (parent[method]) {
                return {
                    parent,
                    method: parent[method],
                };
            } else {
                return this.getDeepMethod(parent.$parent, method);
            }
        },
    },
};
</script>



