<template>
    <div class="sr-dynamic-table">
        <div class="input_table">
            <div class="table_title">
                <el-input
                    class="title"
                    v-model="information.tableTitle"
                    placeholder=""
                    :disabled="disabled"
                ></el-input>
            </div>
            <el-table
                :show-header="false"
                :data="information.tableData"
                border
                :row-class-name="getClassName"
                :cell-class-name="getCellName"
                class="sr-dynamic-table"
                style="width: 100%"
            >
                <el-table-column
                    prop="date"
                    :class="{ selected: true }"
                    v-for="(item, index) in information.arrData"
                    :key="index"
                >
                    <template slot-scope="scope">
                        <!--  -->
                        <div
                            class="bgantopleft"
                            :class="{ bianji2: rowFlag }"
                            @click.stop="editRow(item)"
                            v-if="
                                information.arrData.indexOf(item) == '0' &&
                                information.tableData.indexOf(scope.row) ==
                                    rowIndex &&
                                !disabled
                            "
                        >
                            <!--  -->
                            <slot name="rowcheckicon">
                                <i class="el-icon-more" v-show="rowFlag"></i>
                            </slot>
                            <!--  -->
                            <slot name="rowicon">
                                <i class="el-icon-more-outline" v-show="!rowFlag"></i>
                            </slot>

                            <!--  -->
                            <ul v-show="rowFlag">
                                <li
                                    @click.stop="handleRow(row)"
                                    v-for="(row, index) in rowData"
                                    :key="index"
                                >{{ row.text }}</li>
                            </ul>
                        </div>
                        <!--  -->

                        <el-input
                            @click.stop.native="
                                operate(
                                    information.arrData.indexOf(item),
                                    information.tableData.indexOf(scope.row)
                                )
                            "
                            :maxlength="maxLength"
                            v-model="scope.row[item]"
                            :disabled="disabled"
                        ></el-input>
                        <div
                            class="bgantop"
                            :class="{ bianji: columnFlag }"
                            @click.stop="editColumn(item)"
                            v-if="
                                information.tableData.length - 1 == information.tableData.indexOf(scope.row) &&
                                information.arrData.indexOf(item) ==
                                    columnIndex &&
                                !disabled
                            "
                        >
                            <!-- icon -->
                            <slot name="colicon">
                                <i class="el-icon-more-outline" v-show="!columnFlag"></i>
                            </slot>
                            <!-- icon -->
                            <slot name="colcheckicon">
                                <i class="el-icon-more" v-show="columnFlag"></i>
                            </slot>
                            <!--  -->
                            <ul v-show="columnFlag">
                                <li
                                    @click="handleColumn(row)"
                                    v-for="(row, index) in columnData"
                                    :key="index"
                                >{{ row.text }}</li>
                            </ul>
                        </div>
                    </template>
                </el-table-column>
            </el-table>
        </div>
    </div>
</template>

<script>
export default {
    name: "SrDynamicTable",
    model: {
        prop: "information",
        event: "change",
    },
    props: {
        information: {
            type: [Array, Object],
            default: () => {
                return {
                    tableTitle: "",
                    tableData: [{}, {}, {}],
                    arrData: ["0", "1", "2", "3", "4", "5"],
                };
            },
        },
        // 
        maxLength: {
            type: Number,
            default: () => {
                return 500;
            },
        },
        // 
        maxRows: {
            type: Number,
            default: () => {
                return 10;
            },
        },
        // 
        minRows: {
            type: Number,
            default: () => {
                return 1;
            },
        },
        // 
        maxCols: {
            type: Number,
            default: () => {
                return 10;
            },
        },
        // 
        minCols: {
            type: Number,
            default: () => {
                return 1;
            },
        },
        // true
        disabled: {
            type: Boolean,
            default: () => {
                return false;
            },
        },
    },
    data() {
        return {
            rowIndex: null,
            rowFlag: false,
            columnIndex: null,
            columnFlag: false,
            rowData: [
                {
                    type: "addTop",
                    text: "",
                },
                {
                    type: "addBottom",
                    text: "",
                },
                {
                    type: "delete",
                    text: "",
                },
            ],
            columnData: [
                {
                    type: "addFront",
                    text: "",
                },
                {
                    type: "addBehind",
                    text: "",
                },
                {
                    type: "delete",
                    text: "",
                },
            ],
        };
    },
    mounted() {
        document.querySelector("body").addEventListener("click", () => {
            this.resetData();
        });
    },
    methods: {
        resetData() {
            this.rowFlag = false;
            this.rowIndex = null;
            this.columnFlag = false;
            this.columnIndex = null;
        },
        operate(item, index) {
            this.columnIndex = item;
            this.rowIndex = index;
            this.rowFlag = false;
            this.columnFlag = false;
        },
        // 
        editRow(item, index) {
            this.columnFlag = false;
            this.rowFlag = true;
        },
        // 
        handleRow(row) {
            if (
                row.type === "addTop" &&
                this.information.tableData.length < this.maxRows
            ) {
                this.information.tableData.splice(this.rowIndex, 0, {});
            } else if (
                row.type === "addBottom" &&
                this.information.tableData.length < this.maxRows
            ) {
                this.information.tableData.splice(this.rowIndex + 1, 0, {});
            } else if (
                row.type === "delete" &&
                this.information.tableData.length > this.minRows
            ) {
                this.information.tableData.splice(this.rowIndex, 1);
            } else {
                this.$message.error(
                    `${this.minRows}-${this.maxRows}`
                );
                return false;
            }
            this.resetData();
        },
        // 
        editColumn(item, index) {
            this.columnFlag = true;
            this.rowFlag = false;
        },
        // 
        handleColumn(row) {
            if (
                row.type === "addFront" &&
                this.information.arrData.length < this.maxCols
            ) {
                this.information.arrData.splice(
                    this.columnIndex,
                    0,
                    this.information.arrData.length
                );
            } else if (
                row.type === "addBehind" &&
                this.information.arrData.length < this.maxCols
            ) {
                this.information.arrData.splice(
                    this.columnIndex + 1,
                    0,
                    this.information.arrData.length
                );
            } else if (
                row.type === "delete" &&
                this.information.arrData.length > this.minCols
            ) {
                this.information.arrData.splice(this.columnIndex, 1);
            } else {
                this.$message.error(
                    `${this.minCols}-${this.maxCols}`
                );
                return false;
            }
            this.resetData();
        },
        getClassName(row) {
            if (this.rowFlag) {
                if (row.rowIndex == this.rowIndex) {
                    return "row_border";
                }
            }
        },
        getCellName(row) {
            if (this.columnFlag) {
                if (row.columnIndex == this.columnIndex && row.rowIndex == 0) {
                    return "cell_border bot";
                } else if (row.columnIndex == this.columnIndex) {
                    return "cell_border";
                }
            }
        },
    },
};
</script>


