<template>
  <div class="sr-alert">
    <el-alert
      :class="{ description: description ? true : false }"
      :title="title"
      :type="type"
      :show-icon="showIcon ? true : false"
      :description="description ? description : ''"
      :closable="closable ? true : false"
    >
    </el-alert>
  </div>
</template>

<!--
  
  
