<script>
export default {
  components: {},
  props: {
    title:{
      type:String,
      default(){
        return ""
      }
    },
    type: {
      type: String,
      default() {
        return "success";
      },
    },
    showIcon: {
      type: Boolean,
      default() {
        return true;
      },
    },
    closable: {
      type: Boolean,
      default() {
        return true;
      },
    },
    description: {
      type: [Boolean, String],
      default() {
        return "";
      },
    },
  },
  name: "SrAlert",
  data() {
    return {};
  },
};
</script>

<style scoped >
</style>



