<template>
  <div class="sr-textarea">
    <template>
      <el-input
        :value="value"
        @input="change($event)"
        :disabled="item.disabled"
        :readonly="item.readonly"
        :placeholder="item.placeholder"
        autosize
        clearable
        type="textarea"
        :rows="item.row || 5"
        :resize="item.resize ? item.resize : 'none'"
      ></el-input>
      <span class="el-input__count" v-if="item.maxlength"
        ><span class="current_length">{{ length }}</span
        >/{{ item.maxlength }}</span
      >
    </template>
  </div>
</template>

<!--
  
  
  20201119 17:09:02
  v1.0
-->
<script>
export default {
  model: {
    prop: "value",
    event: "change"
  },
  name: "SrTextarea",
  components: {},
  props: {
    value: String | Number,
    item: {
      type: Object,
      default: {}
    }
  },
  data() {
    return {
      length: 0
    };
  },
  watch: {
    value: {
      handler: function(params) {
        if (params&&params.trim()) {
          this.length = params.length;
        }1
      },
      immediate:true
    }
  },
  methods: {
    change(value) {
      this.length = value.length;
      this.$emit("change", value);
    },
    search() {
      this.$emit("search");
    }
  },
  mounted() {}
};
</script>

<style scoped></style>



