<template>
    <el-row class="relative width-100 uploaderview">
      <el-col :span="4">
        <label
          class="label-item ellipsis"
          :style="{
            height: widthUpHeight,
          }"
          >{{ dataUp.label || label }}</label
        >
      </el-col>
      <el-col :span="20">
        <div class="widthUp" v-if="viewDataS.length" ref="UpBoxHeight">
          <div class="uploaderList">
            <div
              class="uploader"
              v-for="(item, index) in viewDataS"
              :key="index"
              v-show="
                index < (dataUp.colNum || 3) || expand || !dataUp.showExpand
              "
            >
              <i class="sr-icon-attachent"></i>
              <span
                @click="seeclick(item, dataUp)"
                class="uploader_title"
                :title="item.name || item.fileName"
                >{{ item.name || item.fileName }}</span
              >
              <i
                style="color: #1890ff"
                @click="download(item, dataUp)"
                class="sr-icon-download"
              ></i>
            </div>
          </div>
          <div class="expand_box" v-if="viewDataS.length">
            <div
              class="expand text-right more_less pointer"
              @click="expand = !expand"
              v-show="
                dataUp.showExpand && viewDataS.length > (dataUp.colNum || 3)
              "
            >
              <i class="el-icon-arrow-down el-icon--right" v-show="!expand"></i>
              <i class="el-icon-arrow-up el-icon--right" v-show="expand"></i>
            </div>
          </div>
        </div>
        <span class="none" v-else>{{ emptyPlacehoder }}</span>
      </el-col>
    </el-row>
</template>
<script>
export default {
  inject: ["baseUrl", "imgBaseUrl"],
  data() {
    return {
      expand: false,
      viewDataS: [],
      lessImg: require("./link.png"),
      portalsite: "cqportalsite",
      widthUpHeight: "",
    };
  },
  props: {
    labelWidth: {
      type: [String, Number],
      default: () => {
        return 120;
      },
    },
    dataUp: {
      type: Object,
      default: () => {
        return {};
      },
    },
    viewData: {
      type: [String, Array, Object],
      default: () => {
        return [];
      },
    },
    label: String,
    emptyPlacehoder: {
      type: String,
      default: () => {
        // - -
        return "";
      },
    },
  },
  watch: {
    viewData: {
      handler(newName, oldName) {
        this.viewDataS = newName;
        if (typeof newName == "string") {
          this.viewDataS = JSON.parse(newName);
        } else {
          this.viewDataS = newName;
        }
        this.getwidthUpHeight()
      },
      // wacthfirstNamehandler
      deep: true,
    }
  },
  methods: {
    seeclick(file) {
      let token = sessionStorage.getItem("token");
      if (
        [
          "zip",
          "rar",
          "xls",
          "xlsx",
          "doc",
          "docx",
          "txt",
          "html",
          "pdf",
        ].includes(file.name.split(".").pop())
      ) {
        window.open(`${this.baseUrl}/viewPdf?id=${file.id}`);
      } else {
        window.open(
          `${this.baseUrl}/jfzg/file/api/file/download?key=${file.id}&access_token=${token}`
        );
      }
    },
    download(file) {
      let token = sessionStorage.getItem("token");
      this.exportFile(
        `${this.baseUrl}/jfzg/file/api/file/download?key=${file.id}&access_token=${token}`
      );
    },
    /**
     * 
     * @param {String}  
     */
    fieldSubStr(name, maxFeild) {
      return name.length > (maxFeild || 15)
        ? name.substr(0, maxFeild || 15) + "..."
        : name;
    },
    // 
    judgeTrail(filePath) {
      if (!filePath) return false;
      let index = filePath.lastIndexOf(".");
      //
      let ext = filePath.substr(index + 1);
      //
      return ext;
    },
    //
    exportFile(url) {
      let a = document.createElement("a");
      a.href = url;
      a.click();
    },
    getwidthUpHeight() {
      this.$nextTick(() => {
        this.widthUpHeight = this.$refs.UpBoxHeight.offsetHeight + "px";
      });
    },
  },
  mounted() {
    if (this.imgBaseUrl) {
      this.portalsite = this.imgBaseUrl.split("/")[3];
    }
    if (this.viewData && typeof this.viewData == "string") {
      // try {
      //   JSON.parse(this.viewData)
      //   this.viewDataS = JSON.parse(this.viewData);
      // } catch (error) {
      //   this.viewDataS = this.viewData
      // }
      this.viewDataS = JSON.parse(this.viewData);
    } else {
      this.viewDataS = this.viewData;
    }
  },
};
</script>



