<template>
  <el-dialog
    :visible.sync="visible"
    :width="width"
    :before-close="handleClose"
    append-to-body
    custom-class="sr-dialog"
  >
    <div class="dialog_heared">
      <i :class="`el-notification__icon el-icon-${type}`"></i>
      <span> {{ title }}</span>
    </div>
    <div class="dialog_content">
      {{ content }}
    </div>
    <slot name="content" v-if="conetntSlot"></slot>
    <div class="dialog_footer">
      <el-button v-if="type == 'warning'" size="medium" @click="$emit('close')">
        
      </el-button>
      <el-button
        v-if="type == 'warning'"
        size="medium"
        type="primary"
        @click="$emit('ok')"
      >
        
      </el-button>
      <el-button v-else size="medium" @click="$emit('close')" type="primary"
        ></el-button
      >
    </div>
  </el-dialog>
</template>

<script>
export default {
  name: "SrDialog",
  props: {
    // 
    visible: {
      type: Boolean,
      default: function () {
        return false;
      },
    },
    title: {
      type: String,
      default: function () {
        return "";
      },
    },
    type: {
      type: String,
      default: function () {
        return "success";
      },
    },
    content: {
      type: String,
      default: function () {
        return "";
      },
    },
    width: {
      type: String,
      default: function () {
        return "480";
      },
    },
    conetntSlot: {
      type: Boolean,
      default: function () {
        return false;
      },
    },
  },
  data() {
    return {
      dialogVisible: false,
    };
  },
  methods: {
    handleClose() {
      this.$emit("handleClose");
    },
  },
};
</script>

<style></style>



