<template>
    <div class="sr-upload">
        <el-upload
            ref="SrUpload"
            :action="uploadFileUrl"
            :show-file-list="false"
            :on-success="(res, file, fileList) => {imgSuccessFn(item, res, file, fileList)}"
            :before-upload="(file) => {beforeUpload(item, file)}"
			:headers="{'Authorization': `${token_type} ${access_token}`}"
			name="file"
            v-bind="item"
        >
            <!--  -->
            <div class="upload_list_box" v-if="item.limit && value && Array.isArray(value)">
                <div
                    class="el-upload-list--picture-card"
                    v-for="(res, index) in value"
                    :key="index"
                >
                    <img
                        :src="`${previewUrl}?key=${res.id}&access_token=${access_token}`"
                        :style="`width: ${item.width || 86}px; height: ${item.height || 86}px`"
                        class="avatar"
                    />
                    <span class="el-upload-list__item-actions">
                        <span @click.stop="handlePreview(res.id)">
                            <i class="sr-icon-viewlarger"></i>
                        </span>
                        <span @click.stop="handleRemove(res.id)">
                            <i class="sr-icon-ashbin"></i>
                        </span>
                    </span>
                </div>

                <i
                    :style="`width: ${item.width || 86}px; height: ${item.height || 86}px`"
                    class="el-icon-plus avatar-uploader-icon"
                ></i>
            </div>
            <!--  -->
            <div class="upload_file_box" v-else>
                <div class="el-upload-list--picture-card" v-if="value">
                    <img
                        :src="`${previewUrl}?key=${value.id}&access_token=${access_token}`"
                        :style="`width: ${item.width || 86}px; height: ${item.height || 86}px`"
                        class="avatar"
                    />
                    <span @click.stop class="el-upload-list__item-actions">
                        <span @click="handlePreview(value.id)">
                            <i class="sr-icon-viewlarger"></i>
                        </span>
                        <span @click="handleUpload">
                            <i class="sr-icon-upload"></i>
                        </span>
                        <span @click="handleRemove(value.id)">
                            <i class="sr-icon-ashbin"></i>
                        </span>
                    </span>
                </div>

                <i
                    v-else
                    :style="`width: ${item.width || 86}px; height: ${item.height || 86}px`"
                    class="el-icon-plus avatar-uploader-icon"
                ></i>
            </div>
        </el-upload>
        <el-dialog title="" :visible.sync="dialogVisible">
            <img width="100%" :src="dialogImageUrl" alt />
        </el-dialog>
    </div>
</template>

<script>
export default {
    name: "SrUpload",
    inject: ["uploadFileUrl", "previewUrl"],
    props: ["item", "value"],
    model: {
        prop: "value",
        event: "change",
    },
	computed: {
		access_token() {
			return sessionStorage.getItem('token')
		},
		token_type() {
			return sessionStorage.getItem('token_type')
		}
	},
    data() {
        return {
            dialogVisible: false,
            dialogImageUrl: "",
        };
    },
    methods: {
        /**
         * 
         * */
        handlePreview(id) {
            this.dialogImageUrl = `${this.previewUrl}?key=${id}&access_token=${this.access_token}`;
            this.dialogVisible = true;
        },
        handleRemove(id) {
            if (this.item.limit && this.value && Array.isArray(this.value)) {
                let fileList = this.value.filter((res) => {
                    return res.id != id;
                });
                this.$emit("change", fileList);
            } else {
                this.$emit("change", "");
            }
        },
        handleUpload() {
            this.$refs.SrUpload.$children[0].$refs.input.click();
        },
        // 
        beforeUpload(item, file) {},
        // 
        imgSuccessFn(item, res) {
            if (this.item.limit && this.value && Array.isArray(this.value)) {
                // 
				let fileList = this.value
				fileList.push(res.data)
				this.$emit('change', fileList)
            } else {
                // 
				this.$emit('change', res.data)
            }
        },
    },
};
</script>

<style>
</style>


