<template>
    <div class="sr-tag">
        <el-tag
            :key="index"
            v-for="(tag, index) in value"
            closable
            :disable-transitions="false"
            @close="handleClose(tag)"
        >{{tag}}</el-tag>

        <el-popover popper-class="tag_popover" placement="bottom-start" width="304" trigger="focus" v-if="inputVisible">
            <el-table
                @row-click="rowClick"
                :show-header="false"
                :data="tableData"
                style="width: 100%"
            >
                <el-table-column :prop="unitName"></el-table-column>
            </el-table>
            <el-input
                class="input-new-tag"
                v-model="inputValue"
                ref="saveTagInput"
                size="small"
                @input="handleInput"
                @keyup.enter.native="handleInputConfirm"
                slot="reference"
            ></el-input>
        </el-popover>
        <div class="button-new-tag" @click="showInput" v-else>+ </div>
    </div>
</template>


<script>
export default {
    name: "SrTag",
    model: {
        prop: "value",
        event: "change",
    },

    props: {
        value: {
            type: Array,
            default: () => {
                return [];
            },
        },
        // keyvalue
        keyword: {
            type: String,
            default: () => {
                return "keyword";
            },
        },
        // 
        getList: {
            type: Function,
            default: () => {
                return new Promise((resolve, reject) => {
                    return resolve
                });
            },
        },
        // 
        params: {
            type: Object,
            default: () => {
                return {};
            },
        },
        // tag
        unitName: {
            type: String,
            default: () => {
                return "unitName";
            },
        },
    },
    data() {
        return {
            inputVisible: false,
            inputValue: "",
            tableData: [],
        };
    },

    methods: {
        // tag
        handleClose(tag) {
            this.value.splice(this.value.indexOf(tag), 1);
        },
        // 
        showInput() {
            this.inputVisible = true;
            this.$nextTick((_) => {
                this.$refs.saveTagInput.$refs.input.focus();
                this.getList({ ...this.params }).then((res) => {
                    if (res.msg.code === "0000") {
                        this.tableData = res.data.records;
                    }
                });
            });
        },
        // enter
        handleInputConfirm() {
            let inputValue = this.inputValue;
            if (inputValue) {
                this.value.push(inputValue);
            }
            this.inputVisible = false;
            this.inputValue = "";
        },
        // 
        handleInput(val) {
            this.getList({
                ...this.params,
                [this.keyword]: val,
            }).then((res) => {
                if (res.msg.code === "0000") {
                    this.tableData = res.data.records;
                }
            });
        },
        // tag
        rowClick(val) {
            this.value.push(val[this.unitName]);
            this.inputVisible = false;
            this.inputValue = "";
        },
    },
};
</script>

<style scoped>
</style>



