<template>
  <el-select
    :value="value"
    @change="change($event)"
    :placeholder="item.placeholder"
    :popper-append-to-body="!item.popperAppendToBody"
    :no-data-text="nodatatext"
    :multiple="item.multiple"
    :disabled="item.disabled"
    :style="item.multiple ? 'width:100%' : false"
    :filterable="!item.filterable"
    clearable
    class="sr-select"
  >
    <el-option
      v-for="(op, index) in list"
      :key="index"
      :label="item.label ? op[item.label] : op.dataName"
      :value="item.value ? op[item.value] : op.dataValue"
    ></el-option>
  </el-select>
</template>

<!--
  
  
  20201119 17:09:02
  v1.0
-->
<script>
export default {
  name: "SrSelect",
  model: {
    prop: "value",
    event: "change",
  },
  props: {
    value: String | Number | Array,
    item: {
      type: Object,
      default: {},
    },
    nodatatext: {
      type: String,
      default() {
        return "";
      },
    },
    list: {
      type: Array,
      default() {
        return [];
      },
    },
    config: {
      type: Object,
      default() {
        return {
          label: "label",
          value: "value",
        };
      },
    },
  },
  methods: {
    change(value) {
      this.$emit("change", value);
    },
  },
};
</script>

<style scoped >
</style>



