<template>
  <div class="SrMoreText">
    <label
      :style="'width:' + (item.label ? item.labelWidth : 0) + 'px'"
      class="label-item ellipsis"
      ref="label"
      >{{ item.label }} {{ !hideColon ? "" : "" }}</label
    >
    <span ref="SrMoreText" :class="['txt', hid ? 'hid' : 'show',importShow ? 'importShow':'']">{{
      txt
    }}</span>
    <template>
      <el-button class="btn" v-if="overflow" type="text" @click="open"
        >{{ btnTxt }}
        <i v-if="hid" :class="['el-icon-arrow-down', 'el-icon--right']"></i>
        <i v-else :class="['el-icon-arrow-up', 'el-icon--right']"></i
      ></el-button>
    </template>
  </div>
</template>

<!--
  
  
  20210225 10:35:56
  v1.0
-->
<script>
export default {
  model: {
    prop: "value",
    event: "change",
  },
  name: "SrMoreText",
  components: {},
  props: {
    value: String | Number,
    item: {
      type: Object,
      default: {},
    },
    hideColon: Boolean, // 
  },
  data() {
    return {
      hid: null,
      txt: "",
      num: null,
      overflow: null,
      btnTxt: "",
      importShow:null
    };
  },
  watch: {
    value() {
      let divWidth = this.$refs.SrMoreText.offsetWidth; //
      let txtLen = Math.ceil(divWidth / 21); //24321
      let valHeight = this.value.length * txtLen; //
      this.num = 3 * txtLen - Math.ceil(txtLen) / 5;
      let heightLen = Math.ceil(valHeight / divWidth); //
      
      
      if (3 < heightLen) {
        //

        this.txt = this.value.substring(0, Math.ceil(this.num)) + "......";
        this.overflow = true; // 
        this.hid = true; //  
        
      } else {
        this.txt = this.value;
        this.hid = false; //  
        this.importShow = true
      }
    },
  },
  methods: {
    change(value) {
      this.$emit("change", value);
    },
    open() {
      this.hid = !this.hid;
      this.btnTxt = this.hid ? "" : "";

      if (!this.hid) {
        this.txt = this.value;
      } else {
        this.txt = this.value.substring(0, Math.ceil(this.num)) + "......";
      }
    },
  },
  mounted() {
    let divWidth = this.$refs.SrMoreText.offsetWidth; //
    let txtLen = Math.ceil(divWidth / 21); //24321
    let valHeight = this.value.length * txtLen; //
    this.num = 3 * txtLen - Math.ceil(txtLen) / 5;
    let heightLen = Math.ceil(valHeight / divWidth); //
    
    
    if (3 < heightLen) {
      //

      this.txt = this.value.substring(0, Math.ceil(this.num)) + "......";
      this.overflow = true; // 
      this.hid = true; //  
      
    } else {
      this.txt = this.value;
      this.hid = false; //  
      this.importShow = true 
    }
  },
};
</script>

<style scoped lang="scss">
.SrMoreText {
  display: flex;
  position: relative;
  .txt {
    flex-grow: 1;
  }
  .btn {
    position: absolute;
    bottom: 0;
    right: 0;
    padding: 0;
  }
  .hid {
    height: 60px;
    overflow: hidden;
  }
  .show {
    height: auto;
  }
}
</style>



