export function getRules() { // messagetrigger
    const rules = {
      // 
      requiredText: [{
        required: true,
        message: '',
        trigger: 'blur'
      }],
      requiredFalseText: [{
        required: false,
        message: '',
        trigger: 'blur'
      }],
      maxLength: [{ // 
        max: 100,
        trigger: 'blur',
        message: '20'
      }],
      regular: [{ // 
        pattern: /^((1[3456789]\d{9})|(0\d{2,3}-?\d{7,8}))$/,
        message: '',
        trigger: 'blur'
      }],
      validator: [{
        validator: (rule, value, callback) => {
          callback();
        },
        trigger: 'blur'
      }],
      requiredNotText: [{
        required: true,
        message: '',
        trigger: 'change'
      }],
      requiredIsArray: [{
        type: 'array',
        required: true,
        message: '',
        trigger: 'change'
      }],
      // 
      isphone: [{
        pattern: /^((1[3456789]\d{9})|(0\d{2,3}-?\d{7,8}))$/,
        message: '',
        trigger: 'blur'
      }],
      // 
      isPWD: [{
        pattern: /^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{8,20}$/,
        message: '8-20',
        trigger: 'blur'
      }],
      //
      isEmail: [{
        pattern: /^([a-zA-Z]|[0-9])(\w|\-)+@[a-zA-Z0-9]+\.([a-zA-Z]{2,4})$/,
        message: '',
        trigger: 'blur'
      }],
      // 
      textMax20: [{
        max: 20,
        trigger: 'blur',
        message: '20'
      }],
      textMax50: [{
        max: 50,
        trigger: 'blur',
        message: '50'
      }],
      textMax100: [{
        max: 100,
        trigger: 'blur',
        message: '100'
      }],
      // 82
      isMoney: [{
        pattern: /^([1-9]\d{0,7})$|^(0|[1-9]\d{0,7})\.(\d{1,2})$/,
        message: ''
      }],
      //11
      isMoney11: [{
        pattern: /^\d{11}$/,
        message: '11'
      }],
  
      // 
      isNumber: [{
        pattern: /^[1-9]\d*$/,
        message: ''
      }],
      // 
      isArrayList: [{
        type: 'array',
        required: true,
        message: '',
        trigger: 'change'
      }],
      isURL: [{
        pattern: /[a-zA-Z0-9][-a-zA-Z0-9]{0,62}(\.[a-zA-Z0-9][-a-zA-Z0-9]{0,62})+\.?/,
        message: '',
        trigger: 'blur'
      }],
      regularList: {
        Number1: /^(^[1-9](\d+)?(\.\d{1,2})?$)|(^\d\.\d{1,2}$)$/, // 
        Number2: /(^([-]?)[1-9]([0-9]+)?(\.[0-9]{1,2})?$)|(^([-]?)(0){1}$)|(^([-]?)[0-9]\.[0-9]([0-9])?$)/, // 
        Number3: /^[1-9]\d*$/,
        Number4: /^\d+$/,
        Number5: /^(^[0-9](\d{1,17})?(\.\d{1,4})?$)$/, // 
        text1: /^[\u4e00-\u9fa5]{0,}$/, // 
        rate: /^[1-5]$/,
        // 
        IdCard: /^[1-9]\d{5}(18|19|([23]\d))\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{3}[0-9Xx]$/,
        // 
        BankCard: /^([1-9]{1})(\d{12}|\d{15}|\d{18})$/,
        // 
        Email: /^([A-Za-z0-9_\-\.])+\@([A-Za-z0-9_\-\.])+\.([A-Za-z]{2,4})$/,
        NumberText: /^[0-9a-zA-Z]*$/,
      },
      define: [{ // 
        pattern: null,
        message: 'message',
        trigger: 'blur'
      }],
    }
  
    return rules
  }


