<template>
  <div>
    <uploader
      :options="options"
      class="uploader-example sr-big-upload"
      ref="uploader"
      @file-success="fileSuccess"
      @file-added="onFileAdded"
      @file-progress="onFileProgress"
      @file-error="onFileError"
      @file-removed="onFileRemove"
      style="width: 100%"
    >
      <!--  -->
      <div class="uploadDiv">
        <div
          class="uploader-file-info success"
          v-for="(fitem, index) in list.slice(0, 3)"
          :key="'fitem_' + index"
        >
          <div class="file-name">
            <i class="el-icon-paperclip"></i>
            <span
              class="name"
              @click="toDwon(fitem)"
              :title="fitem.name || fitem.fileName"
              >{{ fitem.name || fitem.fileName }}</span
            >
          </div>
          <div class="operate" @click="remove(fitem)">
            <i class="el-icon-close"></i>
          </div>
        </div>
      </div>
      <!--  -->
      <uploader-list>
        <div slot-scope="scope">
          <uploader-file
            :file="itemEr"
            v-for="(itemEr, index) in scope.fileList"
            :key="'itemEr_' + index"
            :list="true"
            @pause="pause"
            @resume="resume"
            @startUpload="startUpload"
          ></uploader-file>
        </div>
      </uploader-list>
      <!-- popover -->
      <el-popover
        popper-class="file_popover"
        placement="top-end"
        width="280"
        trigger="hover"
        v-if="list.length > 3"
      >
        <ul>
          <li
            class="more_file_list"
            v-for="(fitem, index) in list.slice(3)"
            :key="'fitem_' + index"
          >
            <div class="file-name">
              <i class="el-icon-paperclip"></i>
              <span
                class="name"
                @click="toDwon(fitem)"
                :title="fitem.name || fitem.fileName"
                >{{ fitem.name || fitem.fileName }}</span
              >
            </div>
            <div class="operate" @click="remove(fitem)">
              <i class="el-icon-close"></i>
            </div>
          </li>
        </ul>
        <div class="more_file" slot="reference">
          <i class="el-icon-more"></i>
        </div>
      </el-popover>

      <!-- html -->
      <div
        :class="item.disabled ? 'uploaderDisabled upload-tip' : 'upload-tip'"
      >
        <uploader-btn :single="true">
          <el-popover
            v-if="!item.limit && fileList.length"
            placement="top"
            width="82"
            trigger="hover"
            content=""
          >
            <div class="btn" slot="reference">
              <i class="el-icon-refresh-left"></i>
            </div>
          </el-popover>
          <el-popover
            v-else
            placement="top"
            width="82"
            trigger="hover"
            content=""
          >
            <div class="btn" slot="reference">
              <i class="el-icon-plus"></i>
            </div>
          </el-popover>
        </uploader-btn>
      </div>
    </uploader>
  </div>
</template>

<script>
import uploaderFile from "./file.vue";
import { md5Mixin } from "./common/mixins";
import link from "./link.png";
export default {
  name: "SrBigUpload",
  provide() {
    return {
      uploader: this.$refs.uploader,
    };
  },
  props: {
    fileList: {
      type: Array,
      default() {
        return [];
      },
    },
    item: {
      type: Object,
      default() {
        return {};
      },
    },
    form: {
      type: Object,
      default() {
        return {};
      },
    },
    isHide: Boolean,
  },
  inject: ["baseUrl", "imgBaseUrl", "uploadBaseUrl"],
  mixins: [md5Mixin],
  watch: {
    fileList(val) {
      this.list = JSON.parse(JSON.stringify(val));
      this.getUploaderWidth();
    },
  },
  data() {
    return {
      list: JSON.parse(JSON.stringify(this.fileList)),
      linkPng: link,
      fileListMaxLength: 10,
    };
  },
  mounted() {
    this.getUploaderWidth();
  },
  components: {
    uploaderFile,
  },
  methods: {
    toDwon(file) {
      if (
        [
          "zip",
          "rar",
          "xls",
          "xlsx",
          "doc",
          "docx",
          "txt",
          "html",
          "pdf",
        ].includes(file.name.split(".").pop())
      ) {
        window.open(`${this.baseUrl}/viewPdf?id=${file.id}`);
      } else {
        window.open(
          `${this.baseUrl}/jfzg/file/api/file/download?key=${file.id}`
        );
      }
    },
    /**
     * 202px
     */
    getUploaderWidth() {
      if (this.item.target !== "editor" && !this.item.hideMore) {
        this.fileListMaxLength =
          Math.floor(this.$refs.uploader.$el.offsetWidth / 202) - 1;
      }
    },
  },
};
</script>

<style lang="scss" scoped>
</style>



