<template>
  <div>
    <el-dialog
      :visible.sync="dialogVisible"
      width="544px"
      :before-close="handleClose"
      :close-on-click-modal="false"
      :showClose="false"
      class="bzhTableHeader"
    >
      <div class="content_box">
        <div class="content_title"> <span></span></div>
        <div class="content_left">
          <el-input
            @change="searchData"
            placeholder=""
            v-model="search"
          >
          </el-input>
          <i
            @click="searchData(search)"
            style="float: right"
            class="el-icon-search"
          ></i>
          <div class="content_left_lists">
            <div class="content_left_title"></div>
            <div
              class="content_left_list"
              :key="item.prop"
              v-for="item in dataCopy"
            >
              <span class="label">{{ item.label }}</span>
              <el-checkbox
                @change="(e) => changechecbox(e, item)"
                style="float: right"
                v-model="item.isShow"
                :disabled="item.disabled"
              ></el-checkbox>
            </div>
          </div>
        </div>
        <div class="content_right">
          <div class="content_right_title">
            
            <span>{{ selectdata.length }}/{{ dataCopy.length }}</span>
          </div>

          <vuedraggable class="wrapper" ghostClass="ghost" v-model="selectdata">
            <transition-group>
              <div
                :key="item.prop"
                v-for="(item, index) in selectdata"
                class="content_right_list"
              >
                <i style="float: left" class="sr-icon-move1"></i>
                <span>{{ item.label }}</span>
                <i
                  @click="deleteSelectData(item, index)"
                  style="float: right"
                  class="el-icon-close"
                  v-if="!item.disabled"
                ></i>
              </div>
            </transition-group>
          </vuedraggable>
        </div>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="saveHeader"> </el-button>

        <el-button @click="handleClose"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
import vuedraggable from "vuedraggable";

export default {
  name: "SrSettingTable",
  components: { vuedraggable },
  props: {
    dialogVisible: {
      type: Boolean,
      default() {
        return false;
      },
    },
    data: {
      type: Array,
      default() {
        return [];
      },
    },
  },
  updated() {},
  data() {
    return {
      search: "",
      selectdata: [],
      allData: [],
      dataCopy: JSON.parse(JSON.stringify(this.data)),
    };
  },
  mounted() {
    this.changeData(true);
    this.allData = this.dataCopy;
    document.body.ondrop = function (event) {
      event.preventDefault();
      event.stopPropagation();
    };
    document.body.ondragover = function (event) {
      event.preventDefault();
      event.stopPropagation();
    };
    document.body.ondragenter = function (event) {
      event.preventDefault();
      event.stopPropagation();
    };
  },
  methods: {
    handleClose() {
      this.$emit("close");
    },
    searchData(e) {
      this.dataCopy = this.allData.filter(
        (item) => item.label.indexOf(e) != -1
      );
    },
    changechecbox(e, item) {
      if (e == true) {
        item.index = this.selectdata.length;
        item.isShow = true;
        this.selectdata.push(item);
      } else {
        this.changeData();
      }
    },
    deleteSelectData(item, index) {
      this.selectdata.splice(index, 1);
      this.dataCopy.forEach((item1) => {
        if (item.prop == item1.prop) {
          item1.isShow = false;
        }
      });
    },
    changeData(flag) {
      this.selectdata = this.dataCopy.filter((item) => item.isShow);
      if (flag) {
        this.selectdata.sort((a, b) => a.index - b.index);
      }
    },
    saveHeader() {
      this.selectdata.forEach((item1, index) => {
        this.dataCopy.forEach((item) => {
          if (item1.prop == item.prop) {
            item.index = index;
          }
        });
      });
      this.$emit("saveHeaderList", this.dataCopy);
    },
  },
};
</script>
<style lang="scss">
</style>


