<template>
    <section class="Sr_Nav_Page">
        <div class="work-inner">
            <el-row>
                <el-col :span="12">
                    <div class="nav">
                        <img :src="configData[type].image" alt style="width: 48px; height: auto" />
                        <div class="nav_title">
                            <div>
                                <span class="title">
                                    {{ project[configData[type].title] }}
                                </span>
                            </div>
                            <div v-if="configData[type].name && type != '5'">
                                <div class="workNumbering">
                                    <label>{{ configData[type].name }}</label>
                                    <span>
                                        {{ project[configData[type].code] }}
                                    </span>
                                </div>
                            </div>
                             <div v-else class="expertWorkNumbering">
                                <div class="workNumbering">
                                     <label><i class="el-icon-phone"></i> {{ project[configData[type].phone] }}</label>
                                    <label > <i class="el-icon-location-outline"></i> {{project[ configData[type].address ]}}</label>
                                </div>
                            </div>
                        </div>
                    </div>
                </el-col>
                <el-col :span="12"></el-col>
            </el-row>
        </div>
    </section>
</template>
<script>
import planImg from './plan.png'
import projectImg from './project.png'
import yearPlanImg from './yearPlan.jpg'
import taskImg from './task.png'
import zhuanjia from "./zhuanjia.png"
export default {
    name: "SrNavPage",
    props: {
        project: {
            type: Object,
            default() {
                return {};
            },
        },
        type: {
            type: Number,
            default: () => {
                return 1; // 12
            },
        },
    },
    data() {
        return {
            configData: {
                1: {
                    image: planImg,
                    title: 'name',
                    name: '',
                    code: 'code'
                },
                2: {
                    image: projectImg,
                    title: 'projectName',
                    name: '',
                    code: 'projectCode'
                },
                3: {
                    image: yearPlanImg,
                    title: 'yearPlanName',
                    name: '',
                    code: 'yearPlanCode'
                },
                4: {
                    image: taskImg,
                    title: 'taskName'
                },
                5:{
                    title:"name",
                    image: zhuanjia,
                    phone: 'phone',
                    address: 'addressName'
                }
            }
        };
    },
    mounted() {
    },
    methods: {},
};
</script>
<style lang="scss" scoped>
</style>



