<template>
  <el-checkbox-group v-model="checkList" @change="change" class="sr-check-box">
    <el-checkbox
      v-for="op in list"
      :disabled="op.disabled || false"
      :label="item.label ? op[item.label] : op.dataValue"
      :name="op.dataValue"
      :key="op.dataValue"
      >{{ op.dataName }}</el-checkbox
    >
  </el-checkbox-group>
</template>

<!--
  
  
  20201119 17:09:02
  v1.0
-->
<script>
export default {
  name: "SrCheckbox",
  model: {
    prop: "value",
    event: "change",
  },

  props: {
    value: Array,
    item: {
      type: Object,
      default: {},
    },
    list: {
      type: Array,
      default() {
        return [];
      },
    },
  },
  watch: {
    value: {
      handler(val) {
        this.checkList = val;
      },
      deep: true,
    },
  },
  data() {
    return {
      checkList: this.value,
    };
  },
  methods: {
    change(value) {
      this.$emit("change", value);
    },
  },
};
</script>

<style scoped >
</style>



