<template>
  <div class="sr-trace-track">
    <el-dialog
      :visible.sync="dialogVisible"
      width="800px"
      top="5vh"
      append-to-body
      @close="dialogVisible = false"
      class="sr-trace-track-dialog"
    >
      <template slot="title">
        <el-tabs v-model="activeName">
          <el-tab-pane name="first">
            <div slot="label">
              <img
                width="24px"
                style="vertical-align: middle"
                height="auto"
                src="./traceTrack/xuanting.png"
                alt
              />
              <span></span>
            </div>
          </el-tab-pane>
          <el-tab-pane name="second" v-if="imgUrl">
            <div slot="label">
              <img
                width="24px"
                style="vertical-align: middle"
                height="auto"
                src="./traceTrack/lanmu.png"
                alt
              />
              <span></span>
            </div>
          </el-tab-pane>
        </el-tabs>
      </template>
      <div class="dialog-content">
        <template v-if="activeName == 'first'">
          <div v-if="newtraclData.length">
            <el-timeline>
              <el-timeline-item
                :color="activity.color"
                class="track_content"
                v-for="(activity, index) in newtraclData"
                :key="index"
              >
                <div class="track_title title_track_title">
                  <span>
                    {{ activity.name }}
                  </span>
                  <div class="title_track_title_box">
                    <div
                      class="track_item title_track_item"
                      :class="{
                        result:
                          list[defaultProps.name] &&
                          list[defaultProps.name].includes(''),
                        people:
                          list[defaultProps.name] &&
                          list[defaultProps.name].includes(''),
                        time:
                          list[defaultProps.name] &&
                          list[defaultProps.name].includes(''),
                      }"
                      v-for="(list, i) in activity[defaultProps.list]"
                      :key="i"
                    >
                      <div
                        v-if="
                          list[defaultProps.name] &&
                          list[defaultProps.name].includes('')
                        "
                      >
                        <span class="name"
                          >{{ list[defaultProps.name] }}</span
                        >
                        <i
                          :class="[calculateColor(list), calculateIcon(list)]"
                        ></i>
                        <span
                          class="value"
                          :class="calculateColor(list)"
                          v-html="list[defaultProps.value]"
                        ></span>
                      </div>
                      <div
                        v-else-if="
                          list[defaultProps.name] &&
                          list[defaultProps.name].includes('')
                        "
                      >
                        <span class="name"
                          >{{ list[defaultProps.name] }}</span
                        >
                        <span
                          class="value"
                          v-html="list[defaultProps.value]"
                        ></span>
                      </div>
                      <div
                        v-if="
                          list[defaultProps.name] &&
                          list[defaultProps.name].includes('')
                        "
                      >
                        <span class="name"
                          >{{ list[defaultProps.name] }}</span
                        >
                        <span
                          class="value"
                          v-html="list[defaultProps.value]"
                        ></span>
                      </div>
                    </div>
                  </div>
                </div>
                <div class="track_box" v-if="activity.flag">
                  <template>
                    <div
                      class="track_item"
                      :class="{
                        advice:
                          list[defaultProps.name] &&
                          list[defaultProps.name].includes(''),
                        file: list[defaultProps.fileList],
                      }"
                      v-for="(list, i) in activity[defaultProps.list]"
                      :key="i"
                    >
                      <div v-if="list[defaultProps.fileList]">
                        <span
                          class="name"
                          v-if="list[defaultProps.name] === 'fileId'"
                          ></span
                        >
                        <span class="name" v-else
                          >{{ list[defaultProps.name] }}</span
                        >
                        <ul>
                          <li
                            class="file_item"
                            v-for="(item, index) in typeof list[
                              defaultProps.fileList
                            ] == 'string'
                              ? JSON.parse(list[defaultProps.fileList])
                              : list[defaultProps.fileList]"
                            :key="index"
                          >
                            <div class="left" @click="download(item)">
                              <i class="sr-icon-attachent"></i>
                              <span
                                class="file_name"
                                :title="item.name || item.fileName"
                                >{{ item.name || item.fileName }}</span
                              >
                            </div>
                          </li>
                        </ul>
                      </div>
                      <div
                        v-else-if="
                          list[defaultProps.name] &&
                          list[defaultProps.name].includes('')
                        "
                      >
                        <div class="name">{{ list[defaultProps.name] }}</div>
                        <span
                          class="value"
                          v-html="list[defaultProps.value]"
                        ></span>
                      </div>
                    </div>
                  </template>
                </div>
              </el-timeline-item>
            </el-timeline>
          </div>
          <div class="emptyBox" v-else-if="trackData.length == 0">
            <img src="./traceTrack/empty.png" alt />
            <div></div>
          </div>
        </template>

        <template v-if="activeName == 'second'">
          <viewer
            style="background: #fff"
            class="viewer"
            :images="[`data:text/html;base64,${imgUrl}`]"
            alt=""
          >
            <img
              class="flow_img"
              :src="`data:text/html;base64,${imgUrl}`"
              alt=""
            />
          </viewer>
        </template>
      </div>
    </el-dialog>
  </div>
</template>
   
<script>
export default {
  components: {},
  props: {
    defaultProps: {
      type: Object,
      default() {
        return {};
      },
    },
    imgUrl: {
      type: String,
      default() {
        return "";
      },
    },
    trackData: {
      type: Array,
      default() {
        return [];
      },
    },
  },
  watch: {
    trackData() {
      this.trackDataIsShow();
    },
    deep: true,
  },
  data() {
    return {
      activeName: "first",
      dialogVisible: false,
      newtraclData: [],
    };
  },
  methods: {
    handleClick(tab, event) {},
    download(file) {
      let token = sessionStorage.getItem("token");
      this.exportFile(
        `${window.location.origin}/jfzg/file/api/file/download?key=${file.id}&access_token=${token}`
      );
    },
    //
    exportFile(url) {
      let a = document.createElement("a");
      a.href = url;
      a.click();
    },
    open() {
      this.dialogVisible = true;
    },
    calculateColor(item) {
      let color;
      if (item.value == "") {
        color = "success";
      } else if (item.value == "") {
        color = "error";
      }

      return color;
    },
    calculateIcon(item) {
      let color;
      if (item.value == "") {
        color = "el-icon-success";
      } else if (item.value == "") {
        color = "el-icon-error";
      }
      return color;
    },
    trackDataIsShow() {
      this.newtraclData = this.trackData.map((item) => {
        if (item.name == "") {
          item.flag = false;
        } else if (item.list.length > 3) {
          item.flag = true;
        } else {
          item.flag = false;
        }
        return item;
      });
    },
  },
};
</script>
<style lang="scss">
.sr-trace-track-dialog {
  .success {
    padding-left: 3px;
    color: #1890ff !important;
  }
  .error {
    padding-left: 3px;
    color: #ff1313 !important;
  }
}
.viewer {
  background: #fff;
}
.viewer-backdrop {
  background-color: rgba(255, 255, 255, 1) !important;
}
.viewer-navbar {
  background-color: rgba(255, 255, 255, 1) !important;
}
</style>


