<template>
  <div>
    <el-button :plain="true" @click="open2"></el-button>
    <el-button :plain="true" @click="open3"></el-button>
    <el-button :plain="true" @click="open1"></el-button>
    <el-button :plain="true" @click="open4"></el-button>

      <i class="iconfont" style="color:#999999;margin-right:8px">&#xe75d;</i>
  </div>
</template>

<!--
  
  
  20210602 16:01:58
  v1.0
-->
<script>
export default {
  components: {},
  name: "SrMessage",
  props: {},
  data() {
    return {};
  },
  methods: {
    open1() {
      this.$message("");
    },
    open2() {
      this.$message({
        message: "",
        type: "success",
        center:true,
        duration: 0
      });
    },

    open3() {
      this.$message({
        message: "",
        type: "warning",
      });
    },

    open4() {
      this.$message.error("");
    },
  },
};
</script>

<style scoped >
</style>



