<template>
    <div class="sr-duration">
        <div class="sr-duration-hour">
            <el-input v-model.number="value.hour" type="number" size="small" :min="0"></el-input>
            <span class="hour"></span>
        </div>
        <span class="line">-</span>
        <div class="sr-duration-minute">
            <el-input v-model.number="value.minute" type="number" size="small" :min="0" :max="59"></el-input>
            <span class="minute"></span>
        </div>
    </div>
</template>


<script>
export default {
    name: "SrDuration",
    model: {
        prop: "value",
        event: "change",
    },

    props: {
        value: String | Number,
        item: {
            type: Object,
            default: () => {
                return {};
            },
        },
    },
};
</script>

<style scoped>
</style>



